<template>
	<div :class="['chat-item', isOnwer ? 'right' : 'left']">
		<div class="user" @click="handleClickAvatar">
			<img class="avatar" :src="item.head_img" />
		</div>
		<div class="meta">
			<p  class="hd">
				<van-tag color="#00c08b" v-if="item.is_group_owner" class="label">需求人</van-tag>
				<span class="name">{{ isOnwer ? "我" : item.username }}	<i v-show="item.user_type === '1'" class="iconfont iconrenzheng1"></i> <i v-show="item.is_company_user == 1" class="iconfont iconqiye1"></i></span>
				<!-- <van-tag  v-if="userInfo.id != item.user_id" class="tag" type="danger">公司认证</van-tag> -->
				<van-tag v-show="item.company_type != 0"  class="tag type"  plain type="primary">{{item.company_type>1?'贸易商':'生产商'}}</van-tag>
			</p>
			<p style="margin:0 4px;" class="van-ellipsis" v-if="userInfo.id != item.user_id">
				<span class="cp" v-if="item.attrs " v-for="(v,i) in item.attrs" :key="i">{{v}}</span>
			</p>
			<!-- <van-icon v-if="item.user_type === '1'" class="icon" class-prefix="tj-icon" name="renzheng" color="#c11823" style="margin: 0 3px;" /> -->
			<p class="time">{{ item.datetime }}</p>
		</div>
		<div v-if="item.type === '1'" class="content text">
			<span>{{ item.status > 0 ? item.content : "该消息已被屏蔽" }}</span>
		</div>

		<div v-if="item.type === '2'" class="content img">
			<img :src="item.url" alt="" @click="handlePreview" v-if="item.status > 0" />
			<span v-else>该消息已被屏蔽</span>
		</div>
		<div v-if="item.type === '3'" class="content voice" @click="toggleAudio">
			<div v-if="item.status > 0">
				<van-icon v-if="audioPaused" class="icon" class-prefix="tj-icon" name="voice1" />
				<ani-icon-play v-else />
				{{ audioDuration }}"
			</div>
			<span v-else>该消息已被屏蔽</span>
		</div>
		<div v-if="item.type === '4'" class="content video">
			<video class="video" controls="controls" :src="item.url" ref="video" v-if="item.status > 0" />
			<span class="sppb" v-else>该消息已被屏蔽</span>
		</div>
		<div v-if="item.type === '5'" class="content file">
			<a :href="item.url" @click="handleDownload" v-if="item.status > 0">{{
        item.file_name || item.url
      }}</a>
			<span v-else>该消息已被屏蔽</span>
		</div>
		<p class="tisp" v-if="item.user_type == 0 && item.is_group_owner == 0 && userInfo.id == item.user_id"><router-link to="/my-card">为提高对接可信度,请进行用户认证</router-link></p>
		<p class="tisp" v-else-if="item.user_type == 0 && item.is_group_owner == 0 && userInfo.id != item.user_id">该用户暂未实名认证,请谨慎对接</p>
	</div>
</template>

<script>
	import moment from "moment";
	import {
		Icon,
		Row,
		Col,
		Tag
	} from "vant";
	import BenzAMRRecorder from "benz-amr-recorder";
	import AniIconPlay from "./ani-icon-play";
	import {
		mapGetters,
		mapState
	} from "vuex";
	export default {
		name: "chat-item",
		props: ["item", "isOnwer"],
		components: {
			[Icon.name]: Icon,
			[Row.name]: Row,
			[Tag.name]: Tag,
			[Col.name]: Col,
			AniIconPlay
		},
		data() {
			return {
				publicPath: process.env.BASE_URL,
				moment,
				audioPaused: true,
				audioDuration: "0",
				amr: null,
				id: null,
			};
		},
		async created() {
			let dm = this.item;
			localStorage.setItem("itemid", this.item.user_id);
		},
		mounted() {
			this.id = sessionStorage.getItem("tianji-v2-z-userid")
			if (this.item.type === "3") {
				this.amr = new BenzAMRRecorder();
				this.amr.initWithUrl(this.item.url).then(() => {
					this.audioDuration = this.amr.getDuration();

					this.amr.onPause(() => {
						console.log("onPause");
						this.audioPaused = true;
					});
					this.amr.onStop(() => {
						console.log("onStop");
						this.audioPaused = true;
					});
					this.amr.onEnded(() => {
						console.log("onEnded");
						this.audioPaused = true;
					});
					this.amr.onAutoEnded(() => {
						console.log("onAutoEnded");
						this.audioPaused = true;
					});
					this.amr.onPlay(() => {
						console.log("onPlay");
						this.audioPaused = false;
					});
				});
			}
		},
		computed: {
			...mapGetters(["userInfo"]),
		},
		async beforeDestroy() {
			if (this.amr) {
				this.amr.stop();
				delete this.amr;
			}
		},
		methods: {
			toggleAudio() {
				if (this.amr) {
					if (this.amr.isPlaying()) {
						this.amr.pause();
					} else {
						this.amr.play();
					}
				} else {
					this.amr = new BenzAMRRecorder();
					this.amr.initWithUrl(this.item.url).then(() => {
						this.audioDuration = this.amr.getDuration();
						this.amr.play();
					});
				}
			},
			handlePreview() {
				this.$emit("preview-image", this.item.record_id);
			},
			handleDownload(e) {
				e.preventDefault();
				this.$dialog
					.confirm({
						title: "温馨提示",
						message: `您即将前往该文件的下载页面，是否继续?`,
						confirmButtonText: "继续",
						cancelButtonText: "稍后再说"
					})
					.then(() => {
						// var form = document.createElement('form');
						// form.action = item.src;
						// document.getElementsByTagName('body')[0].appendChild(form);
						// form.submit();
						window.location.href = this.item.url;
					})
					.catch(() => {
						// on cancel
						console.log("cancel dialog");
					});
			},
			handleClickAvatar() {
				this.$emit("click-avatar", this.item);
			}
		}
	};
</script>

<style lang="less" scoped>
.hd{
	margin:5px 0;
}	
	.iconrenzheng1,.iconqiye1{
		color: #D30023;
		margin:0 2px;
	}	
	.tag{
		margin:0 5px;
	}
	.cp{
		color: #0c5460;
		 background-color: #d1ecf1;
		 border-color: #bee5eb;
		font-size: 12px;
		margin:5px 4px 0 0;
		width: 158px;
		padding:4px;
		border-radius:8px;
		text-align: center;
	}
	.type{
		color: #156ec7;
	}
	.chat-item {
		position: relative;
		padding: 10px 60px;
		min-height: 60px;
		box-sizing: border-box;
		overflow: hidden;

		.user {
			position: absolute;
			top: 10px;

			.avatar {
				display: block;
				width: 40px;
				height: 40px;
				border-radius: 5px;
			}
		}

		.meta {
			font-size: 12px;
			// padding: 0 0 10px;
			// display: flex;
			align-items: flex-end;
			flex-wrap: wrap;
			.name {
				font-weight: 600;
				font-size: 13px;
			}

			.time {
				padding: 0 5px;
				margin:5px 0;
			}

			.level-icon {
				width: 20px;
				vertical-align: middle;
				display: inline-block;
				margin-right: 7px;
			}
		}

		.content {
			text-align: left;
			position: relative;
			display: inline-block;
			max-width: 100%;
			min-width: 80px;
			min-height: 36px;
			box-sizing: border-box;
			font-size: 14px;
			line-height: 22px;
			padding: 7px 10px;
			border-radius: 3px;
			color: #333;
			background-color: #F1F1F1;
			word-break: break-all;

			&::after {
				content: "";
				position: absolute;
				top: 15px;
				width: 0;
				height: 0;
				border-style: solid dashed dashed;
				border-color: #f1f1f1 transparent transparent;
				overflow: hidden;
				border-width: 10px;
			}

			&.text {
				white-space: pre-line;
				user-select: all;
			}

			&.img,
			&.video {
				background: none;
				padding: 0;
				overflow: hidden;

				&::after {
					display: none;
				}

				img {
					display: block;
					max-height: 120px;
					max-width: 100%;
				}
			}
		}

		.video {
			height: 120px;
			width: 214px;
			background-color: #ddd;
		}

		&.left {
			text-align: left;

			.user {
				left: 8px;
			}

			.meta {
				flex-direction: row;

				.label {
					margin-right: 5px;
				}
			}

			.content {
				&::after {
					left: -10px;
				}
			}
		}

		&.right {
			text-align: right;

			.user {
				right: 8px;
			}

			.meta {
				flex-direction: row-reverse;

				.label {
					margin-left: 5px;
				}
			}

			.content {
				&::after {
					right: -10px;
				}
			}
		}
	}

	.tisp {
		color: #999999;
		font-size: 12px;
		margin-top: 5px;
	}

	@media screen and (min-width: 750px) {
		.tag{
			margin:0 5px;
		}
		.cp{
			background:#f1f1f1;
			font-size: 12px;
			margin:5px 0;
			color: #666;
			width: 158px;
			padding:2px 0;
			border-radius:2px;
			text-align: center;
		}
		.tisp {
			color: #999999;
			font-size: 12px;
			margin-top: 5px;
		}
		.chat-item {
			padding: 10px 60px;
			min-height: 60px;

			.user {
				top: 10px;

				.avatar {
					width: 40px;
					height: 40px;
					border-radius: 5px;
				}
			}

			.meta {
				font-size: 12px;
				padding: 0 0 10px;

				.name {
					font-size: 13px;
				}

				.time {
					padding: 0 5px;
				}

				.level-icon {
					width: 20px;
					margin-right: 7px;
				}
			}

			.content {
				min-width: 80px;
				min-height: 36px;
				font-size: 14px;
				line-height: 22px;
				padding: 7px 10px;
				border-radius: 3px;

				&::after {
					top: 15px;
					border-width: 10px;
				}

				&.img,
				&.video {
					img {
						max-height: 120px;
					}
				}
			}

			.video {
				height: 120px;
				width: 214px;
			}

			&.left {
				.user {
					left: 8px;
				}

				.meta {
					.label {
						margin-right: 5px;
					}
				}

				.content {
					&::after {
						left: -10px;
					}
				}
			}

			&.right {
				.user {
					right: 8px;
				}

				.meta {
					.label {
						margin-left: 5px;
					}
				}

				.content {
					&::after {
						right: -10px;
					}
				}
			}
		}
	}

	.sppb {
		position: absolute;
		right: 0;
	}
</style>
