<template>
  <div class="avatar-wrapper" @click="handleClick">
    <img :src="item.head_img" alt="" class="avatar" />
    <span class="name">{{ item.username || item.name }}</span>
    <van-tag v-if="isOwner" color="#00c08b" class="label">需求人</van-tag>
    <van-tag v-if="isSelected" color="#c11823" class="label">解决者</van-tag>
  </div>
</template>

<script>
import { Tag } from "vant";

export default {
  name: "butts-page",
  components: {
    [Tag.name]: Tag
  },
  props: ["item", "isOwner", "isSelected"],
  methods: {
    handleClick() {
      this.$emit("click", this.item);
    }
  }
};
</script>
<style lang="less" scoped>
.avatar-wrapper {
  display: inline-block;
  position: relative;
  width: 46px;
  text-align: center;
  margin-bottom: 10px;

  .avatar {
    width: 46px;
    height: 46px;
    box-sizing: border-box;
    margin-bottom: 5px;
    border-radius: 50%;
    display: block;
    border: 1px solid #eee;
    background-color: #eee;
  }

  .name {
    display: inline-block;
    max-width: 70%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 12px;
  }

  .label {
    // display: none;
    position: absolute;
    top: 33px;
    left: 0px;
    border-radius: 8px;
    padding: 3px 5px;
    font-size: 12px;
    line-height: 1em;
  }
}

@media screen and (min-width: 750px) {
  .avatar-wrapper {
    width: 46px;
    margin-bottom: 10px;

    .avatar {
      width: 46px;
      height: 46px;
      margin-bottom: 5px;
    }

    .name {
      font-size: 12px;
    }

    .label {
      top: 33px;
      left: 0px;
      border-radius: 8px;
      padding: 3px 5px;
      font-size: 12px;
    }
  }
}
</style>
