<template>
  <van-popup
    v-model="visible"
    :position="position"
    :overlay="overlay"
    @close="onClose"
    :close-on-click-overlay="false"
    class="qr-popup"
  >
    <div class="qr-box">
      <div class="title">
        添加天集社工作人员
        <br />
        我们将为您提供对接服务
      </div>

      <img :src="`${publicPath}img/service-qr.png`" class="qr" />
    </div>

    <van-icon
      class="close"
      class-prefix="tj-icon"
      name="close"
      @click="onClose"
    />
  </van-popup>
</template>

<script>
import { mapGetters } from "vuex";
import { Popup, Icon } from "vant";

export default {
  name: "qr-popup",
  components: {
    [Popup.name]: Popup,
    [Icon.name]: Icon
  },
  props: {
    paid: String,
    src: String,
    title: String
  },
  data() {
    return {
      publicPath: process.env.BASE_URL,
      visible: false,
      position: "center",
      overlay: true
    };
  },
  computed: {
    ...mapGetters(["settings"])
  },
  methods: {
    show() {
      this.visible = true;
    },
    hide() {
      this.visible = false;
    },
    onClose() {
      this.hide();
    }
  }
};
</script>

<style lang="less">
.qr-popup {
  border-radius: 10px;
  padding: 20px;
  box-sizing: border-box;
  background: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  .qr-box {
    width: 60vw;
    height: 60vw;
    border-radius: 5px;
    background-color: #fff;
    padding: 10px;
    text-align: center;
  }

  .title {
    font-size: 16px;
  }

  .close {
    margin-top: 20px;
    font-size: 36px;
    color: #fff;
  }

  .qr {
    width: 160px;
    display: block;
    margin: 15px auto;
  }

  .tips {
    font-size: 12px;
    font-weight: 400;
    color: rgba(94, 104, 117, 1);
    // color: rgba(254, 104, 11, 1);
    margin-top: 13px;
    text-align: center;
  }
}
</style>
