<template>
  <van-popup
    v-model="showPicker"
    position="bottom"
    @close="calsp"
    :overlay="true"
  >
    <van-picker
      show-toolbar
      :columns="columns"
      @cancel="cal"
      @confirm="onConfirm"
    />
  </van-popup>
</template>

<script>
import { Picker, Popup } from "vant";
export default {
  props: ["msg"],
  name: "demand-source",
  components: {
    [Picker.name]: Picker,
    [Popup.name]: Popup
  },
  data() {
    return {
      value: "",
      showPicker: false,
      columns: ["本公司需求", "代发布需求"]
    };
  },
  methods: {
    hide() {
      this.showPicker = false;
    },
    show() {
      this.showPicker = true;
    },
    onConfirm(value, index) {
      this.value = value;
      this.hide();
      console.log(value, index);
      // sessionStorage.setItem("seltvalue",value)
      this.$emit("val", value, index, this.showPicker);
    },
    cal() {
      this.hide();
      this.$emit("cval", this.showPicker);
    },
    calsp() {
      console.log(111);
      this.hide();
      this.$emit("cpop", this.showPicker);
    }
  },

  watch: {
    msg(val) {
      console.log(val);
      this.showPicker = val;
    }
  }
};
</script>
