<template>
	<van-row type="flex" justify="space-around" class="tisp" v-show="showTisp">
	  <van-col span="24">
		  <div class="tips_box">
			  <van-image
			    width="60"
			    height="60"
				fit="contain"
			    src="http://demo.oramage.com/demo/tianji-vue-v3/share.jpg"
			  />
			  <p>
				  <span style="font-weight: bold;">天集社-SCM垂直整合供应链平台</span><br/>
				  <span>一键关注服务号，接收审核通知等实时消息提醒，不错过每一个商机。</span>
			  </p>
			  <van-button round type="danger" class="bg" size="small" to="/follow-us">关注</van-button>
			  <van-icon class="icop" name="cross" color="#fff" @click="showTisp = false"/>
		  </div>
	  </van-col>
	</van-row>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import {checkSubscribe } from "@/api/base";

import {
	  Button,
	  Icon,
	  Row,
	  Col,
	  Image
} from "vant";
export default{
	components:{
		[Button.name]: Button,
		[Icon.name]: Icon,
		[Row.name]: Row,
		[Col.name]: Col,
		[Image.name]: Image,
	},
	data(){
		return{
			showTisp:false
		}
	},
	mounted() {
		this.fetchUserInfo(this.userInfo.id);
		checkSubscribe(this.userInfo.id).then(res=>{
			console.log(res)
			if(res == 0 ){
				this.showTisp = true
			}
		})
	},
	computed:{
		...mapGetters(["userInfo"]),
	},
	methods:{
		...mapActions(["fetchUserInfo"]),
	}
}
</script>

