<template>
  <div class="action-icon" @click="handleClick">
    <van-icon class="icon" :class-prefix="iconPrefix" :name="icon" />
    <span class="name">{{ name }}</span>
  </div>
</template>

<script>
import { Icon } from "vant";
export default {
  props: ["icon", "name", "iconPrefix"],
  components: {
    [Icon.name]: Icon
  },
  methods: {
    handleClick() {
      this.$emit("click");
    }
  }
};
</script>

<style lang="less" scoped>
.action-icon {
  text-align: center;
  display: flex;
  padding: 5px 10px;
  flex-direction: column;
  color: #666;

  .icon {
    font-size: 30px;
  }

  .name {
    font-size: 13px;
  }
}
@media screen and (min-width: 750px) {
  .action-icon {
    padding: 5px 10px;

    .icon {
      font-size: 30px;
    }

    .name {
      font-size: 13px;
    }
  }
}
</style>
