import fetch from "../utils/fetch";
import { API_BASE_URL } from "../config";
import qs from "qs";

// 公众号授权重定向链接
export function redirectOCAuth(redirectUrl = window.location.href) {
	let search = qs.parse(window.location.search);
	let { user_id: inviters } = search;
	if (!inviters) {
	  const hashSearch = qs.parse(window.location.hash.split("?")[1]);
	  if (hashSearch) {
	    inviters = hashSearch.user_id;
	  }
	}
	// ?inviter=inviters
  const uri = `${API_BASE_URL}/user_login?url=${encodeURIComponent(
    redirectUrl
  )}`;
  window.location.replace(uri);
}

// 公众号登录
export function loginByWXOC(code, inviter) {
  return fetch({
    url: "/user_info",
    method: "get",
    params: {
      code,
      inviter
    }
  });
}

// 获取公众号jssdk签名
export function getJSSDKSign() {
  return fetch({
    url: "/jssign",
    method: "get",
    params: {
      url: location.href.split("#")[0]
    }
  });
}

