<template>
  <div class="activity-page">
    <van-nav-bar
      fixed
      border
      :title="activity.title"
      left-arrow
      @click-left="goBack"
    />

    <van-panel class="activity">
      <van-cell class="activity-title" slot="header">
        <div slot="title" class="title">
          <van-tag
            class="tag"
            v-if="activity.activityStatus === 1"
            color="#ff6e1d"
            >报名中</van-tag
          >
          <van-tag
            class="tag"
            v-if="activity.activityStatus === 2"
            color="#00c08b"
            >已截止</van-tag
          >
          <van-tag class="tag" v-if="activity.activityStatus === 3"
            >已结束</van-tag
          >
          <span class="title-text">{{ activity.title }}</span>
        </div>
      </van-cell>
      <van-cell class="supplier-item" title="">
        <div class="label" slot="label">
          <div>截止报名时间：{{ activity.deadline_time }}</div>
          <div>活动时间：{{ activity.activity_time }}</div>
          <div>活动地点：{{ activity.activity_site }}</div>
        </div>
      </van-cell>
      <div class="activity-content" v-html="activity.introduction"></div>
    </van-panel>

    <van-row class="bottom-bar">
      <van-button
        block
        square
        type="danger"
        @click="dialogVisible = true"
        v-if="activity.activityStatus === 1 && !activity.is_apply"
        >马上报名</van-button
      >

      <van-button
        square
        disabled
        block
        v-if="activity.activityStatus === 2 && !activity.is_apply"
        >已截止报名</van-button
      >

      <van-button
        square
        disabled
        block
        v-if="activity.activityStatus !== 3 && activity.is_apply"
        >您已报名</van-button
      >

      <van-button square disabled block v-if="activity.activityStatus === 3"
        >活动已结束</van-button
      >
    </van-row>

    <van-dialog
      v-model="dialogVisible"
      title="报名人数"
      show-cancel-button
      @close="dialogVisible = false"
      @confirm="handleComfirm"
    >
      <van-cell>
        <van-field
          style="background: #eee"
          v-model="count"
          type="number"
          placeholder="请输入参加人数"
        />
      </van-cell>
    </van-dialog>
  </div>
</template>
<script>
import moment from "moment";
import { mapState, mapGetters } from "vuex";
import {
  Button,
  Row,
  Col,
  NavBar,
  CellGroup,
  Field,
  Dialog,
  Panel,
  Cell,
  PullRefresh,
  List,
  Icon,
  Tag,
  ImagePreview
} from "vant";
import { getActivityDetail, applyActivity } from "../api/v3";
import { setShare } from "../wechat";
import { PAGE_BASE_URL } from "../config";

export default {
  name: "about",
  components: {
    [Row.name]: Row,
    [Col.name]: Col,
    [NavBar.name]: NavBar,
    [Button.name]: Button,
    [Cell.name]: Cell,
    [Icon.name]: Icon,
    [Tag.name]: Tag,
    [List.name]: List,
    [Dialog.name]: Dialog,
    [Field.name]: Field,
    [PullRefresh.name]: PullRefresh,
    [Panel.name]: Panel,
    [CellGroup.name]: CellGroup
  },
  data() {
    return {
      moment,
      count: "",
      dialogVisible: false,
      activity: {}
    };
  },
  computed: {
    ...mapState(["from"]),
    ...mapGetters(["userInfo"]),
    activityId() {
      return this.$route.params.id;
    }
  },

  mounted() {
    this.fetchData();
  },
  methods: {
    async fetchData() {
      const activity = await getActivityDetail(
        this.activityId,
        this.userInfo.id
      );

      activity.introduction = this.escape2Html(activity.introduction);

      this.activity = activity;

      const desc = `活动：${this.activity.title}。点击查看需求详情`;
      const link = PAGE_BASE_URL + "#/activities/" + this.activityId;
      setTimeout(() => {
        setShare({
          title: "天集社", // 分享标题
          desc,
          link
        });
      }, 1000);
    },
    escape2Html(str) {
      var arrEntities = { lt: "<", gt: ">", nbsp: " ", amp: "&", quot: '"' };
      return str.replace(/&(lt|gt|nbsp|amp|quot);/gi, function(all, t) {
        return arrEntities[t];
      });
    },
    // 点击返回按钮
    goBack() {
      if (this.from) {
        this.$router.back();
      } else {
        this.$router.replace("/activities");
      }
    },
    async handleComfirm() {
      try {
        await applyActivity({
          user_id: this.userInfo.id,
          activity_id: this.activityId,
          num: this.count
        });
        this.activity.is_apply = 1;
        this.$toast.success("恭喜您报名成功！");
      } catch (error) {
        this.$toast.fail(error.message || "请输入正确的数量");
      }
    },
    // 预览图片
    previewImg(index) {
      ImagePreview({
        images: this.images,
        startPosition: index,
        lazyLoad: true,
        onClose() {
          // do something
          console.log("close image preview");
        }
      });
    }
  }
};
</script>

<style lang="less" scoped>
.cell-group {
  margin-top: 12px;
}

.activity-page {
  padding-bottom: 45px;
}

.logo-box {
  width: 100%;
  height: 60vw;
  display: flex;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  background-color: #fff;
}

.title {
  align-items: center;
  max-width: 100%;
  .title-text {
    font-size: 16px;
    margin-left: 5px;
  }
}

.tag {
  vertical-align: middle;
  margin-top: -3px;
  border-radius: 5px;
  margin-left: 7px;
}

.label {
  font-size: 14px;
  line-height: 1.7em;
  color: #999;
  text-align: left;

  > div {
    width: 100%;
  }
}

.bottom-bar {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  display: flex;
}

.activity-header {
  font-size: 13px;
  color: #999;
}

.activity-content {
  padding: 10px 15px;
  color: #999;
  line-height: 1.75em;
}

.imgDiv {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 100%;
  border: 1px solid #ccc;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.1);

  img {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    height: 100%;
    display: block;
  }
}

@media (min-width: 750px) {
  .cell-group {
    margin-top: 12px;
  }

  .activity-page {
    padding-bottom: 45px;
  }

  .logo-box {
    width: 100%;
    height: 750px * 0.6;
  }

  .title {
    .title-text {
      font-size: 16px;
      margin-left: 5px;
    }
  }

  .tag {
    margin-top: -3px;
    border-radius: 5px;
    margin-left: 7px;
  }

  .label {
    font-size: 14px;
  }

  .activity-header {
    font-size: 13px;
  }

  .activity-content {
    padding: 10px 15px;
  }
}
</style>

<style lang="less">
.activity-content {
  padding: 10px 15px;

  img {
    max-width: 100% !important;
  }
}
</style>
