import Vue from "vue";
import Vuex from "vuex";
import { getUserInfo, checkSubscribe } from "./api/base";
import { fetchDemandTypes, getChannel } from "./api/demand";
import { getIndustryList } from "./api/v3";
import { getSupplierTypes } from "./api/v3";
Vue.use(Vuex);

const userId = sessionStorage.getItem("tianji-v2-z-userid");

export default new Vuex.Store({
  state: {
    userInfo: {
      id: userId,
      score: "0"
    },
    demandTypes: [],
    supplierTypes: [{ type_id: "0", name: "全部领域" }],
    industries: { all: [] },
    channels: [],
    from: undefined
  },
  getters: {
    userInfo: state => {
      return state.userInfo;
    },
    demandTypes: state => {
      return state.demandTypes.map(item => {
        return {
          id: item.type_id,
          name: item.type_name
        };
      });
    },
    supplierTypes: state => {
      return state.supplierTypes.map(item => {
        return {
          id: item.type_id,
          name: item.name
        };
      });
    },
    industries: state => {
      return state.industries;
    },
    channels: state => {
      return state.channels.map(item => {
        return {
          id: item.id,
          name: item.channel_name
        };
      });
    }
  },
  mutations: {
    updateDemandTypes: (state, payload) => {
      state.demandTypes = payload;
    },
    updateSupplierTypes: (state, payload) => {
      state.supplierTypes = [{ type_id: "0", name: "全部领域" }, ...payload];
    },
    updateIndustries: (state, payload) => {
      const { list, pid } = payload;
      state.industries = {
        ...state.industries,
        [pid]: list.map(item => {
          return {
            id: item.industry_id,
            name: item.industry_name
          };
        })
      };
    },
    updateChannel: (state, payload) => {
      state.channels = payload;
    },
    updateSubscribe: (state, payload) => {
      state.userInfo.is_subscribe = payload;
    },
    updateUserInfo: (state, payload) => {
      // function formatAttrs(attrs) {
      //   const a = attrs ? attrs.split("/") : [];
      //   const len = a.length;
      //   let last = "";
      //   if (len > 2) {
      //     last = a.pop();
      //   }
      //   return len > 2 ? [a.join("/"), last] : a;
      // }
      // state.userInfo = {
      //   ...payload,
      //   attrs: formatAttrs(payload.attrs)
      // };
      state.userInfo = payload;
    },
    setFromRoute: (state, payload) => {
      state.from = payload;
    }
  },
  actions: {
    fetchDemandTypes: async ({ commit }, payload) => {
      const res = await fetchDemandTypes(payload);
      commit("updateDemandTypes", res);
    },
    fetchSupplierTypes: async ({ commit }, payload) => {
      const res = await getSupplierTypes(payload);
      commit("updateSupplierTypes", res);
    },
    checkSubscribe: async ({ commit }, payload) => {
      const res = await checkSubscribe(payload);
      commit("updateSubscribe", res);
    },
    fetchIndustries: async ({ commit }, payload) => {
      const list = await getIndustryList(payload);
      commit("updateIndustries", { ...payload, list });
    },
    fetchChannel: async ({ commit }) => {
      const res = await getChannel({ limit: 1000 });
      commit("updateChannel", res);
    },
    fetchUserInfo: async ({ commit }, payload) => {
      const res = await getUserInfo(payload);
      commit("updateUserInfo", res);
    }
  }
});
