import fetch from "../utils/fetch";


export function userLabels(data) {
  return fetch({
    url: "user_labels",
    method: "get",
    params: data
  });
}

export function userSetLabel(data) {
  return fetch({
    url: "user_set_label",
    method: "post",
    data
  });
}

export function companyUploadAgreement(data) {
  return fetch({
    url: "company_upload_agreement",
    method: "post",
    headers: {
      "Content-Type": "multipart/form-data"
    },
    data
  });
}

