import Vue from "vue";
import Router from "vue-router";
import MtaH5 from "mta-h5-analysis";
import TabbarLayout from "./views/TabbarLayout";
import DemandLayout from "./views/DemandLayout";
import Activity from "./views/Activity";
import ActivityDetail from "./views/ActivityDetail";
import SupplierList from "./views/supplier/index";
import SupplierDetail from "./views/supplier/detail";
import Connections from "./views/Connections";
import UserCenter from "./views/user-center";
import DemandList from "./views/demand/index";
import NewDemand from "./views/demand/create";
import Xgdeman from "./views/demand/xgdeman";
// import DemandDetail from "./views/demand/detail";
import BlackDemand from "./views/BlackDemand";
import store from "./store";
// import {loginByWXOC} from "./api/demand.js";
import { redirectOCAuth, loginByWXOC } from "./api/base";
import {isHouseAgent} from "./api/v4.js"
import { getUserInfo} from "@/api/base.js";
import qs from "qs";

Vue.use(Router);

const router = new Router({
  routes: [
    {
      path: "/",
      redirect: "/demands",
      component: TabbarLayout,
      // meta: { keepAlive: true },
      children: [
        {
          path: "demands",
          name: "demand-list",
          component: DemandList,
          // meta: { keepAlive: true, y: 0 }
        },
        {
          path: "connections",
          name: "connection-list",
          component: Connections,
          meta: { keepAlive: true, y0: 0, y1: 0, y2: 0 }
        },
        {
          path: "suppliers",
          name: "supplier-list",
          component: SupplierList,
          meta: { keepAlive: true, y: 0 }
        },
        {
          path: "activities",
          name: "activity-list",
          component: Activity
        },
        {
          path: "user-center",
          name: "user-center",
          component: UserCenter
        }
      ]
    },
    {
      path: "/demands/new",
      name: "create-demand",
      component: NewDemand
    },
    {
      path: "/demands/xg",
      name: "Xgdeman",
      component: Xgdeman
    },
    {
      path: "/BlackDemand",
      name: "BlackDemand",
      component: BlackDemand
    },

    {
      path: "/demands/:id/",
      name: "demand-detail",
      component: DemandLayout
    },
    {
      path: "/demands/:id/solve",
      name: "solve-demand",
      component: () => import("./views/demand/solve")
    },
    {
      path: "/demands/:id/apply",
      name: "apply-demand",
      component: () => import("./views/demand/apply")
    },
    {
      path: "/my-card",
      name: "my-card",
      component: () => import("./views/MyCard")
    },
    {
      path: "/incharge",
      name: "incharge",
      component: () => import("./views/Incharge")
    },
    {
      path: "/scores",
      name: "scores",
      component: () => import("./views/Scores")
    },
    {
      path: "/feedback",
      name: "feedback",
      component: () => import("./views/Feedback")
    },
    {
      path: "/records",
      name: "records",
      component: () => import("./views/Records")
    },
    {
      path: "/cards/:id",
      name: "cards",
      component: () => import("./views/Card")
    },
    {
      path: "/suppliers/:id",
      name: "supplier-detail",
      component: SupplierDetail
    },
    {
      path: "/activities/:id",
      name: "activity-detail",
      component: ActivityDetail
    },
    {
      path: "/follow-us",
      name: "follow-us",
      meta: { title: "关于我们" },
      component: () => import("./views/FollowUs")
    },
	{
	  path: "/agent/index",
	  name: "agent",
	  meta: { title: "天集社房源代理人管理系统", requireAuth: true},
	  component: () => import("./views/agent/index"),
	  children: [
			{
			  path: "/agentTab",
			  name: "agentList",
			  // component: Connections,
			  meta: { keepAlive: true, y0: 0, y1: 0, y2: 0,requireAuth: true}
			},
		],
	},
	{
		path: "/customerList/:id",
		name: "customerList",
		meta: { title: "天集社房源代理人管理系统", requireAuth: true},
		component: () => import("./views/agent/customerList"),
	},
	{
		path: "/identity",
		name: "identity",
		meta: { title: "天集社房源代理人管理系统",requireAuth: true,},
		component: () => import("./views/agent/identity"),
	},
	{
		path: "/integral/index",
		name: "integral",
		meta: { title: "天集社" },
		component: () => import("./views/integral/index"),
	},
	{
		path: "/integral/recharge",
		name: "recharge",
		meta: { title: "天集社" },
		component: () => import("./views/integral/recharge"),
	},
	{
		path: "/integral/cash",
		name: "cash",
		meta: { title: "天集社" },
		component: () => import("./views/integral/cash"),
	},
	{
		path: "/integral/buy",
		name: "buy",
		meta: { title: "天集社" },
		component: () => import("./views/integral/buy"),
	},
	{
		path: "/Member",
		name: "Member",
		meta: { title: "天集社" },
		component: () => import("./views/Member"),
	},
	{
		path: "/workshop/index",
		name: "workshop",
		meta: { title: "天集社" },
		component: () => import("./views/workshop/index"),
	},
	{
		path: "/workshop/details/:id",
		name: "details",
		meta: { title: "天集社" },
		component: () => import("./views/workshop/details"),
	},
	{
		path: "/consumption",
		name: "/consumption",
		meta: { title: "天集社" },
		component: () => import("./views/consumption"),
	},
	{
		path: "/dock",
		name: "/dock",
		meta: { title: "天集社" },
		component: () => import("./views/dock"),
	},
	{
		path: "/subscribe",
		name: "/subscribe",
		meta: { title: "天集社" },
		component: () => import("./views/Subscribe"),
	},
	{
		path: "/settled",
		name: "/settled",
		meta: { title: "天集社" },
		component: () => import("./views/Settled"),
	},
	 
  ],
  scrollBehavior(to, form, savedPosition) {
    if (savedPosition) {
      return savedPosition;
    } else if (to.hash) {
      return {
        selector: to.hash
      };
    } else {
      return { x: 0, y: 0 };
    }
  }
});

router.onReady(async (to, from) => {
  // 保存 from route
  store.commit("setFromRoute", from);
  if (store.state.userInfo.id) {
    await store.dispatch("checkSubscribe", store.state.userInfo.id);
    if (store.state.userInfo.is_subscribe !== "1") {
      // router.replace("/follow-us");
	  console.log("未关注用户")
    }
  }
});

router.beforeEach((to, from, next) => {
  // 保存 from route
  store.commit("setFromRoute", from);
  next();
  if(to.meta.requireAuth){
  	isHouseAgent().then(res=>{
  		if(res.is_agent == 0){
  			router.replace("/identity");
  		}else{
  			next();
  		}
  	})  
	getUserInfo(sessionStorage.getItem("tianji-v2-z-userid")).then(res=>{
		console.log(res)
		if(res.status != 2){
			router.replace("/identity");
		}else{
			next();
		}
	})
  }else{
  	next();  
  }
});

router.afterEach(() => {
  if (process.env.NODE_ENV === "production") {
    MtaH5.pgv();
  }
});

export default router;
