<template>
  <div class="request-page" ref="box"> <!-- request-page --> <!-- ref="box" -->
	 <tips/> 
	  <div  class="register_ts" v-show="isShow != 2" >
		  <div class="register_ts_img">
			  <p>积分大礼包</p>
			  <van-divider class="newdivider">新用户专享</van-divider>
			  <van-button
			     slot="action"
			     block
			     size="small"
			     type="danger"
			     class="prt"
				 @click.stop="gotoRegister"
			     >立即注册</van-button
			   >
			   <img  @click="clickShow" class="register_clone" src="../../../public/img/close.png" >
		  </div>
	  </div>
	  <van-swipe :class="{'op':!domHide}" class="banner" :autoplay="3000">
	    <van-swipe-item v-for="item in banner" :key="item.id">
	      <a :href="item.url" >
			  <!-- <van-image
			    width="100%"
			    height="140"
				 lazy-load
			    :src="item.img_wap"
			  /> -->
	        <img :src="item.img_wap" style="width: 100%;height: 100%;object-fit: cover;" />
	      </a>
	    </van-swipe-item>
	  </van-swipe>
	<!-- :class="{'op':!domHide}" -->
	<van-row  v-if="domHide" type="flex" justify="space-between" class="index_cf">
	  <van-col span="11"class="v-col">
		  <div class="room_box" @click="gotoYX">
			  <div class="room_box_frist">
				  <p>空间严选</p>
				  <p>优质一手房源</p>
			  </div>
			  <div class="room_box_last">
				  <img src="../../../public/img/img2.png" >
			  </div>
		  </div>
	  </van-col>
	    <van-col span="11" class="v-col">
			<div class="room_box corl" @click="gotoGY">
			  <div class="room_box_frist">
				  <p>找供应商</p>
				  <p>全产业链供应</p>
			  </div>
			  <div class="room_box_last">
				  <img src="../../../public/img/img1.png" >
			  </div>
			</div>
		</van-col>
	</van-row>
	  

    <div action="/" class="toolbar van-hairline--bottom "  :class="{'fiex1':fieds}">
		  <h3>最新需求</h3>
		  <van-button class="address-btn" block size="small" @click="showAreaPopup">
		    {{ selectedAreaName || "选择地区" }}
		    <van-icon name="arrow-down" class="icon" />
		  </van-button>
		  <form action="/" class="search-form">
		    <van-search
		      class="search"
		      v-model="keyword"
		      placeholder="搜索需求..."
		      @search="onSearch"
		    />
		  </form>
    
    </div>
	<van-button
	   slot="action"
	   block
	   size="small"
	   type="danger"
	   class="request-btn"
	   @click="handleCreateDemand"
	   >发布需求</van-button
	 >
	<div class="tabs van-hairline--bottom " :class="{'fiex2':fieds}">
	  <div
	    :class="['tab', selectedDemandTypeId === '0' && 'active']"
	    @click="selectDemandType('0')"
	  >
	    全部
	  </div>
	  <div
	    v-for="item in demandTypes"
	    :key="item.id"
	    :class="['tab', selectedDemandTypeId === item.id && 'active']"
	    @click="selectDemandType(item.id)"
	  >
	    {{ item.name }}
	  </div>
	</div>
    <div class="main-content" ref="mainContent" @scroll="handleContentScroll">
      <van-pull-refresh
        v-model="refreshing"
        @refresh="onRefresh"
        @scroll="a => console.log(a)"
        class="refresh-content"
      >
        <van-list
          v-show="demandList.length > 0"
          class="content-list"
          v-model="loading"
          :finished="finished"
          finished-text="没有更多了"
          @load="loadMoreDemandList"
        >
          <demand-item
            @click="handleDemandClick([demand.id, demand.user_id])"
            v-for="demand in demandList"
            :key="demand.id"
            :demand="demand"
          />
        </van-list>

        <div class="no-content" v-if="demandList.length === 0">
          <img src="../../assets/no-content-color.png" alt="" />
          <p><strong>暂无需求</strong></p>
          <p>当前行业暂无需求，快来发布吧~</p>
        </div>
      </van-pull-refresh>
    </div>

    <area-popup
      ref="areaPopup"
      :value="selectedAreaCode"
      @confirm="selectArea"
    />
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import {
  Search,
  Button,
  Icon,
  Swipe,
  SwipeItem,
  List,
  PullRefresh,
  Tag,
  Row,
  Col,
  Divider,
  Image 
  
} from "vant";
import AreaPopup from "../../components/area-popup";
import DemandItem from "../../components/demand-item";
import tips from "../../components/tips";
import {
  fetchBannerByDemandType,
  // addCountForDemand,
  fetchDemandList
} from "../../api/demand";

export default {
  name: "DemandListPage",
  components: {
    [Button.name]: Button,
    [Icon.name]: Icon,
    [Search.name]: Search,
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
    [List.name]: List,
    [PullRefresh.name]: PullRefresh,
    [Tag.name]: Tag,
	[Row.name]: Row,
	[Col.name]: Col,
	[Divider.name]: Divider,
	[Image.name]: Image,
    AreaPopup,
    DemandItem,
	tips
  },
  data() {
    return {
      timerId: null,
      keyword: "",
      title: "",
      selectedArea: [],
      banner: [],
      demandList: [],
      refreshing: false,
      loading: false,
      finished: false,
      page: 0,
      pagesize: 20,
	  isShow:2,
	  visible:true,
	  domHide:true,
	  fieds:false,
	  cookie:null
    };
  },
  computed: {
    ...mapGetters(["demandTypes", "userInfo"]),
    selectedDemandTypeId() {
      return (
        // (this.demandTypes[0] && this.demandTypes[0].id) ||
        this.$route.query.type || "0"
      );
    },
    selectedAreaCode() {
      const len = this.selectedArea.length;
      if (len > 0) {
        return this.selectedArea[len - 1].code;
      } else {
        return "100100";
      }
    },
    selectedAreaName() {
      const len = this.selectedArea.length;
      if (len > 0) {
        return this.selectedArea.map(item => item.name).join(" ");
      } else {
        return "";
      }
    }
  },
  watch: {
    selectedDemandTypeId() {
      this.onRefresh();
    }
  },

  async mounted() {
	await this.fetchUserInfo(this.userInfo.id);
	this.isShow = this.userInfo.status
	//console.log(this.isShow)
    await this.fetchDemandTypes({ limit: 1000 });
    this.onRefresh();
	 document.addEventListener('scroll',this.Scroll,true)
  },

  beforeRouteEnter(to, from, next) {
    next(vm => {
      //console.log("beforeRouteEnter", vm.$route.meta.y);
      vm.$refs["mainContent"].scrollTop = vm.$route.meta.y;
    });
  },

  methods: {
    ...mapActions(["fetchDemandTypes", "fetchUserInfo"]),
	gotoYX(){
		this.$router.push("/workshop/index")
	},
	gotoGY(){
		this.$router.push("/suppliers")
	},
	clickShow(){
		if(this.isShow != 2){
			sessionStorage.setItem("cokies","2")
			this.isShow = sessionStorage.getItem("cokies")
			//console.log(this.isShow)
		}
	},
	Scroll(e){
		// console.log(this.$refs.box.scrollTop, this.$refs.box.scrollTop>=100)
		if(this.$refs.box.scrollTop>170 || this.$refs.mainContent.scrollTop>=100){
			// alert(111)
			this.fieds = true;
			this.domHide = false
		}else if(this.$refs.box.scrollTop<80 || this.$refs.mainContent.scrollTop>=80){
			this.fieds = false;
			this.domHide = true
		}
	},
	gotoRegister(){
		this.$router.push("/my-card")
	},
    handleContentScroll(e) {
      // 节流；
      if (this.timerId) clearTimeout(this.timerId);
      this.timerId = setTimeout(() => {
        this.$route.meta.y = e.target.scrollTop;
      }, 300);
    },

    // 处理“发布需求”按钮点击事件
    async handleCreateDemand() {
      await this.fetchUserInfo(this.userInfo.id);
      if (this.userInfo.is_black == 1) {
        this.$dialog
          .alert({
            title: "温馨提示",
            message: "您没有此权限，请联系平台管理员\n（电话：15602908103）",
            confirmButtonText: "确认"
          })
          .then(() => {
            //console.log("confirm dialog");
          });
        return;
      }
      if (this.userInfo.status !== "2") {
        this.$dialog
          .confirm({
            title: "温馨提示",
            message: "您需要注册后才能进行该操作哦",
            confirmButtonText: "马上注册",
            cancelButtonText: "稍后再说"
          })
          .then(() => {
			  this.$router.push("/my-card");
     //        if (this.userInfo.is_subscribe === "1") {
     //          this.$router.push("/my-card");
     //        } else {
     //          // this.$router.replace("/follow-us");
			  // console.log("未注册用户")
     //        }
          })
          .catch(() => {
            // on cancel
            console.log("cancel dialog");
          });
      } else {
        if (this.userInfo.is_subscribe === "1") {
          this.$router.push("/demands/new");
        } else {
          // this.$router.replace("/follow-us");
		  console.log("未注册用户")
        }
      }
    },

    async handleDemandClick(id) {
      //console.log(id);
      localStorage.setItem("usoId", id[1]);
      localStorage.setItem("demId", id[0]);
	  this.$router.push(`/demands/${id[0]}`);
    //   if (this.userInfo.status !== "2") {
    //     this.$dialog
    //       .confirm({
    //         title: "温馨提示",
    //         message: "您需要注册后才能进行该操作哦",
    //         confirmButtonText: "马上注册",
    //         cancelButtonText: "稍后再说"
    //       })
    //       .then(() => {
    //         if (this.userInfo.is_subscribe === "1") {
    //           this.$router.push("/my-card");
    //         } else {
    //           // this.$router.replace("/follow-us");
			 //  console.log("未注册用户")
    //         }
    //       })
    //       .catch(() => {
    //         // on cancel
    //         console.log("cancel dialog");
    //       });
    //   } else {
    //     if (this.userInfo.is_subscribe === "1") {
    //       // await addCountForDemand(id);
    //       this.$router.push(`/demands/${id[0]}`);
    //     } else {
    //       // this.$router.replace("/follow-us");
		  // console.log("未注册用户")
    //     }
    //   }
    },

    onSearch(val) {
      this.title = val;
      this.onRefresh();
    },
    showAreaPopup() {
      this.$refs["areaPopup"].show();
    },
    selectArea(val) {
      this.selectedArea = val;
      this.onRefresh();
    },
    selectDemandType(id) {
      this.$router.replace({ name: "demand-list", query: { type: id } });
    },
    async getBannerByDemandType() {
      const res = await fetchBannerByDemandType({
        limit: 10,
        type: this.selectedDemandTypeId
      });
      this.banner = res;
    },
    async onRefresh() {
      // this.$refs["mainContent"].scrollTop = 0;
      this.getBannerByDemandType();
      this.page = 1;
      const list = await this.loadDemandList();
      this.demandList = list;
      this.refreshing = false;
    },
    async loadMoreDemandList() {
      this.page = this.page + 1;
      const list = await this.loadDemandList();
      this.demandList = [...this.demandList, ...list];
      this.loading = false;
    },
    async loadDemandList() {
      // 异步更新数据
      // if (!this.selectedDemandTypeId) return [];
      const res =
        (await fetchDemandList({
          page: this.page,
          limit: this.pagesize,
          type: this.selectedDemandTypeId,
          title: this.keyword,
          address: this.selectedAreaName
        })) || [];

      if (!Array.isArray(res) && typeof res === "object") {
        const keys = Object.keys(res);
        const newRes = keys.map(k => res[k]);
        this.finished = newRes.length < this.pagesize;
        return newRes;
      }

      this.finished = res.length < this.pagesize;

      return res;
    }
  }
};
</script>

