<template>
  <van-popup v-model="visible" position="bottom" :overlay="true">
    <van-picker
      show-toolbar
      title="选择行业"
      value-key="name"
      :columns="columns"
      @cancel="onCancel"
      cancel-button-text="清除选择"
      @change="onChange"
      @confirm="onConfirm"
      :loading="loading"
    />
  </van-popup>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import { Picker, Popup } from "vant";

export default {
  name: "industry-popup",
  components: {
    [Picker.name]: Picker,
    [Popup.name]: Popup
  },
  props: {
    confirm: Function,
    value: String,
    withAll: Boolean
  },
  data() {
    return {
      columns: [
        {
          values: [],
          className: "level-1",
          defaultIndex: 0
        },
        {
          values: [],
          className: "level-2",
          defaultIndex: 0
        }
      ],
      selectedValues: [],
      visible: false,
      loading: false
    };
  },
  computed: {
    ...mapGetters(["industries"]),
    column1Values() {
      if (this.withAll) {
        return this.industries["0"]
          ? [{ id: "all", name: "全部行业" }, ...this.industries["0"]]
          : [{ id: "all", name: "全部行业" }];
      } else {
        return this.industries["0"] ? this.industries["0"] : [];
      }
    },
    column1Index() {
      const selectedColumn1 = this.selectedValues[0];
      if (selectedColumn1) {
        const index = this.column1Values.findIndex(
          item => item.id === selectedColumn1.id
        );
        return index >= 0 ? index : 0;
      } else {
        return 0;
      }
    },
    column2Values() {
      const selectedColumn1 = this.selectedValues[0];
      if (selectedColumn1 && selectedColumn1.id !== "all") {
        return this.industries[selectedColumn1.id];
      } else {
        return [];
      }
    },
    column2Index() {
      const selectedColumn2 = this.selectedValues[1];
      if (selectedColumn2) {
        const index = this.column2Values.findIndex(
          item => item.id === selectedColumn2.id
        );
        return index >= 0 ? index : 0;
      } else {
        return 0;
      }
    }
  },
  async mounted() {
    await this.fetchIndustriesWithPid("0");
    const [defautl1Name, defautl2Name] = this.value.split("/");
    const default1 =
      this.column1Values.find(item => item.name === defautl1Name) ||
      this.column1Values[0];
    this.selectedValues[0] = default1;
    let default2 = undefined;
    if (default1) {
      await this.fetchIndustriesWithPid(default1.id);
      default2 = this.column2Values.find(item => item.name === defautl2Name);
      this.selectedValues[1] = default2;
    }
    this.columns = [
      {
        className: "level-1",
        values: this.column1Values,
        defaultIndex: this.column1Index
      },
      {
        className: "level-2",
        values: this.column2Values,
        defaultIndex: this.column2Index
      }
    ];
  },
  methods: {
    ...mapActions(["fetchIndustries"]),
    show() {
      this.visible = true;
    },
    hide() {
      this.visible = false;
    },
    onCancel(val) {
      console.log(val);
      this.$emit("clear", []);
      this.hide();
    },
    onConfirm(val) {
      console.log(val);
      this.$emit("confirm", val);
      this.hide();
    },
    async onChange(picker, values) {
      const [column1Value] = values;
      let column2 = this.industries[column1Value.id];
      if (!column2) {
        await this.fetchIndustriesWithPid(column1Value.id);
        column2 = this.industries[column1Value.id];
      }
      picker.setColumnValues(1, column2);
    },
    async fetchIndustriesWithPid(pid) {
      if (!this.industries[pid]) {
        this.loading = true;
        await this.fetchIndustries({ pid });
        this.loading = false;
      }
    }
  }
};
</script>
