<template>
  <div class="incharge-page">
    <van-swipe class="banner" :autoplay="3000">
      <van-swipe-item
        v-for="item in banner"
        :key="item.id"
        v-if="item.activity_type == 0"
      >
        <div
          @click="handleClick(item)"
          class="content"
          v-lazy:background-image="item.img_wap"
          :type="item.activity_type"
        >
          <span class="title">{{ item.title }}</span>
        </div>
      </van-swipe-item>
    </van-swipe>
    <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="loadMore"
      >
        <van-cell v-for="item in arr" :key="item.id" @click="handleClick(item)">
          <div slot="title" class="title">
            <van-tag
              class="tag"
              v-if="item.activity_status === 1"
              color="#ff6e1d"
              >报名中</van-tag
            >
            <van-tag
              class="tag"
              v-if="item.activity_status === 2"
              color="#00c08b"
              >已截止</van-tag
            >
            <van-tag class="tag" v-if="item.activity_status === 3"
              >已结束</van-tag
            >
            <span>{{ item.title }}</span>
          </div>

          <div slot="label" style="margin-top: 10px;">
            <div>
              <van-icon name="clock" style="margin: 0 2px;" />
              {{ item.activity_time }}
            </div>
            <div>
              <van-icon class-prefix="tj-icon" name="biaodiandidian" />
              {{ item.activity_site }}
            </div>
          </div>
          <div slot="icon">
            <img
              v-lazy="item.img_wap"
              style="width: 129px; height: 71px; display: block; margin-right: 10px;"
              alt=""
            />
          </div>
        </van-cell>
      </van-list>
    </van-pull-refresh>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
import {
  Button,
  Swipe,
  SwipeItem,
  Row,
  Col,
  NavBar,
  List,
  Cell,
  Icon,
  PullRefresh,
  Tag
} from "vant";
import { getActivities } from "../api/base";

export default {
  name: "activities",
  components: {
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
    [Row.name]: Row,
    [Col.name]: Col,
    [List.name]: List,
    [Icon.name]: Icon,
    [Cell.name]: Cell,
    [PullRefresh.name]: PullRefresh,
    [NavBar.name]: NavBar,
    [Tag.name]: Tag,
    [Button.name]: Button
  },
  data() {
    return {
      arr: {},
      banner: [],
      list: [],
      page: 0,
      limit: 20,
      loading: false,
      finished: false,
      refreshing: false
    };
  },
  computed: {
    ...mapGetters(["userInfo"])
  },
  async mounted() {
    this.getBanner();
    this.arr = await getActivities({
      page: this.page,
      limit: this.limit
    });
  },
  methods: {
    async onRefresh() {
      this.page = 1;
      const list = await this.onLoad();
      this.list = list;
      this.refreshing = false;
    },
    async loadMore() {
      this.page = this.page + 1;
      const list = await this.onLoad();
      this.list = [...this.list, ...list];
      this.loading = false;
    },
    async onLoad() {
      // 异步更新数据
      const res = await getActivities({
        type: 1,
        page: this.page,
        limit: this.limit
      });
      this.finished = res.length < this.limit;
      return res;
    },

    handleClick(item) {
      if (item.activity_url) {
        let url = item.activity_url;
        if (!/^(http|https):\/\/.*/.test(url)) {
          url = window.location.protocol + "//" + url;
        }
        window.location.href = url;
      } else {
        this.$router.push({ name: "activity-detail", params: { id: item.id } });
      }
    },

    async getBanner() {
      this.banner = await getActivities({
        type: 0,
        page: "1",
        limit: "20"
      });
      // this.banner= this.banner.sort((a,b)=>{
      //   let x = a.ord;
      //   let y = b.ord;
      //   return ((x > y) ? -1 : 1)
      //  })
      console.log(this.banner);
    },
    getTypeName(type) {
      switch (type) {
        case "0":
          return "积分申请";
        case "1":
          return "定向申请";
        case "2":
          return "需求申请";
        case "3":
          return "用户注册";
      }
    }
  }
};
</script>

<style lang="less" scoped>
.banner {
  height: 177px;

  .content {
    display: block;
    width: 100%;
    height: 100%;
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;

    .title {
      display: block;
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      background: rgba(0, 0, 0, 0.5);
      color: #fff;
      height: 2.5em;
      line-height: 2.5em;
      padding: 0 15px;
      font-size: 16px;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
}

.title .tag {
  border-radius: 3px;
  margin-right: 3px;
}

.score-box {
  height: 150px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #c11823;
  color: #fff;

  .score {
    font-size: 32px;
  }
}

.cell-group {
  margin-top: 12px;
}

.intro {
  padding: 10px 15px;
  background-color: #fff;

  h2 {
    font-size: 18px;
    color: #000;
  }

  p {
    font-size: 12px;
    color: #666666;
    margin-bottom: 1em;
  }
}

@media (min-width: 750px) {
  .banner {
    height: 354px;

    .content {
      .title {
        padding: 0 15px;
        font-size: 16px;
      }
    }
  }

  .title .tag {
    border-radius: 3px;
    margin-right: 3px;
  }

  .score-box {
    height: 150px;

    .score {
      font-size: 32px;
    }
  }

  .cell-group {
    margin-top: 12px;
  }

  .intro {
    padding: 10px 15px;

    h2 {
      font-size: 18px;
    }

    p {
      font-size: 12px;
    }
  }
}
</style>
