<template>
  <div id="app">
	  <keep-alive>
	    <router-view class="page" v-if="this.$route.meta.keepAlive" />
	  </keep-alive>
	  
	  <router-view class="page" v-if="!this.$route.meta.keepAlive" />
    <!-- <transition name="van-slide-right">
      
    </transition>
    <transition name="van-slide-right">
    </transition> -->
  </div>
</template>

<script>
import { mapGetters, mapState } from "vuex";
import { initJsSDK } from "./wechat";
import qs from "qs";
import { redirectOCAuth, loginByWXOC } from "./api/base";
export default {
  name: "app",
  data() {
    return {
      oldScrollTop: 0
    };
  },
  created(){
    let search = qs.parse(window.location.search);
    // 路由中没有 code 参数
    if (!search || !search.code) {
      //测试userid
	   // sessionStorage.setItem("tianji-v2-z-userid","");
      const userId = sessionStorage.getItem("tianji-v2-z-userid");
	  
	   // alert(userId)
      // 本地没有缓存用户 ID
      // const userId = this.userInfo.id
      if (userId == "" || userId == null) {
        redirectOCAuth();
      } 
    } else {
      const { code } = search;
      let { user_id: inviter } = search;
      if (!inviter) {
        const hashSearch = qs.parse(window.location.hash.split("?")[1]);
        if (hashSearch) {
          inviter = hashSearch.user_id;
        }
      }
	  sessionStorage.setItem("code",code)
	  sessionStorage.setItem("inviter",inviter)
      loginByWXOC(code, inviter).then(res => {
        console.log(res);
        if (res.is_subscribe == 0) {
          // alert("follow")
          // router.replace("/follow-us");
		  console.log("未注册用户")
        }
		 sessionStorage.setItem("token",res.token)
        sessionStorage.setItem("tianji-v2-z-userid", res.id);
        window.location.search = "";
      });
    }
	
  },
  mounted() {
    initJsSDK();
    if (this.$store.state.userInfo.id) {
      this.$store.dispatch("fetchChannel");
      this.$store.dispatch("checkSubscribe", this.$store.state.userInfo.id);
      this.$store.dispatch("fetchUserInfo", this.$store.state.userInfo.id);
    }

    document.body.addEventListener("focusin", () => {
      //软键盘弹起事件
      /* 获取窗口滚动条高度 */
      function getScrollTop() {
        let scrollTop = 0;
        if (document.documentElement && document.documentElement.scrollTop) {
          scrollTop = document.documentElement.scrollTop;
        } else if (document.body) {
          scrollTop = document.body.scrollTop;
        }
        return scrollTop;
      }

      this.oldScrollTop = getScrollTop() || 0; // 记录当前滚动位置
    });

    document.body.addEventListener("focusout", () => {
      //软键盘关闭事件
      const ua = window.navigator.userAgent;
      if (ua.indexOf("iPhone") > 0 || ua.indexOf("iPad") > 0) {
        //键盘收起页面空白问题
        document.body.scrollTop = this.oldScrollTop;
        document.documentElement.scrollTop = this.oldScrollTop;
      }
    });
  },
  computed:{
     ...mapGetters(["userInfo"]),
  }
};
</script>

