<template>
  <van-popup v-model="visible" position="bottom" :overlay="true">
    <van-area
      title="选择城市"
      v-model="value"
      :columns-num="2"
      :area-list="areaList"
      @confirm="onConfirm"
      @cancel="clear"
      cancel-button-text="清除选择"
    />
  </van-popup>
</template>

<script>
import { Area, Popup } from "vant";
import areaList from "../utils/area";

export default {
  name: "about",
  components: {
    [Area.name]: Area,
    [Popup.name]: Popup
  },
  props: {
    confirm: Function,
    value: String
  },
  data() {
    return {
      areaList,
      visible: false
    };
  },
  methods: {
    show() {
      this.visible = true;
    },
    hide() {
      this.visible = false;
    },
    onConfirm(val) {
      this.$emit("confirm", val);
      this.hide();
    },
    clear() {
      this.$emit("confirm", "");
      this.hide();
    }
  }
};
</script>
