<template>
  <div class="create-request-page">
    <van-nav-bar
      fixed
      border
      title="发布需求"
      left-arrow
      @click-left="onClickLeft"
    />
    <van-cell-group class="cell-group">
      <van-field
        v-model="selectedDemandType.name"
        is-link
        readonly
        required
        label="需求类型"
        placeholder="请选择您的需求类型"
        :error-message="errors.type"
        input-align="right"
        error-message-align="right"
        @click="showDemandTypePopup"
      />
      <!-- <van-field
        v-model="selectedIndustry"
        is-link
        readonly
        label="行业"
        placeholder="请选择您的行业"
        input-align="right"
        error-message-align="right"
        :error-message="errors.industry"
        @click="showIndustryPopup"
      /> -->
      <van-field
        v-if="demand_type == '0'"
        v-model="form.title"
        required
        clearable
        label="需求名称"
        placeholder="如：开关电源采购，5-10K/月"
        :error-message="errors.title"
        input-align="right"
        error-message-align="right"
        @input="errors.title = ''"
      />
      <van-field
        v-else-if="demand_type == '1'"
        v-model="form.title"
        required
        clearable
        label="需求名称"
        placeholder="如：找定位手表方案，预算4万"
        :error-message="errors.title"
        input-align="right"
        error-message-align="right"
        @input="errors.title = ''"
      />

      <van-field
        v-else-if="demand_type == '2'"
        v-model="form.title"
        required
        clearable
        label="需求名称"
        placeholder="如：合金件加工，6K/月"
        :error-message="errors.title"
        input-align="right"
        error-message-align="right"
        @input="errors.title = ''"
      />
      <van-field
        v-else
        v-model="form.title"
        required
        clearable
        label="需求名称"
        placeholder="请填写您的需求名称"
        :error-message="errors.title"
        input-align="right"
        error-message-align="right"
        @input="errors.title = ''"
      />

      <div style="position: relative;">
        <van-field
          v-if="demand_type == '0' || demand_type == '2'"
          clearable
          label="需求数量"
          placeholder=""
          :error-message="errors.num"
          input-align="right"
          error-message-align="right"
        />
        <div v-else-if="demand_type == '1'"></div>
        <van-field
          v-else
          v-model="form.num"
          clearable
          label="需求数量"
          placeholder="请输入您需求的数量"
          :error-message="errors.num"
          input-align="right"
          error-message-align="right"
        />
        <!-- 数量 -->
        <div class="creatNum" v-if="demand_type == '0' || demand_type == '2'">
          <div
            v-for="(num, index) in mock"
            :key="index"
            @click="actives(num, index)"
            :class="[index == stud ? 'bechoice' : '']"
          >
            {{ num.name }}
          </div>
        </div>
      </div>

      <van-field
        v-if="demand_type == '0' || demand_type == '1' || demand_type == '2'"
        :value="selectedAreaName"
        is-link
        readonly
        required
        label="交货地"
        placeholder="请选择交货地"
        input-align="right"
        error-message-align="right"
        :error-message="errors.address"
        @click="showAreaPopup"
      />
      <van-field
        v-else
        :value="selectedAreaName"
        is-link
        readonly
        required
        label="需求地址"
        placeholder="请选择您的地址"
        input-align="right"
        error-message-align="right"
        :error-message="errors.address"
        @click="showAreaPopup"
      />
      <van-cell required title="需求描述" :border="false" />
      <van-field
        v-if="demand_type == '0'"
        class="textarea text2"
        v-model="form.describe"
        type="textarea"
        :error-message="errors.describe"
        @input="errors.describe = ''"
        placeholder="请输入详细需求，如：开关电源采购，MOQ:5-10K/月，交货周期20天，账期3个月。电池（24V 15A 360W），尺寸215*114*50，带参数标签，电压选择标签，内部端子打胶。请生产厂家直接对接，在线对接请上传各自产品资料。"
      />

      <van-field
        v-else-if="demand_type == '1'"
        class="textarea text2"
        v-model="form.describe"
        type="textarea"
        :error-message="errors.describe"
        @input="errors.describe = ''"
        placeholder="请输入详细需求，如：找定位手表方案，预算4万，应用场景：企业内部员工做人员管理使用，2G+GPS方案，支持接收电话/短信、震动提醒、位置定位。IP4防水等级，需对接我方平台。"
      />

      <van-field
        v-else-if="demand_type == '2'"
        class="textarea text2"
        v-model="form.describe"
        type="textarea"
        :error-message="errors.describe"
        @input="errors.describe = ''"
        placeholder="请输入详细需求，如：合金件加工，供料加工，严守公差，要求保密协议，数量6K/月，交货周期20天，账期三个月。"
      />
      <van-field
        v-else
        class="textarea"
        v-model="form.describe"
        type="textarea"
        :error-message="errors.describe"
        @input="errors.describe = ''"
        placeholder="请具体描述一下您的需求，需求越具体效率越高哦"
      />
    </van-cell-group>
    <van-cell-group>
      <van-row style="padding: 10px 15px; display: flex">
        <van-col span="20">
          <van-row gutter="15">
            <van-col span="6" v-for="(item, index) in imgList" :key="index">
              <div class="imgDiv">
                <img :src="item.url" @click="previewImg(index)" />
                <van-icon name="close" @click="delImg(index)"></van-icon>
              </div>
            </van-col>
            <van-col span="6" v-if="imgList.length < 4">
              <div class="uploader" @click="onUpload"></div>
            </van-col>
          </van-row>
        </van-col>

        <van-col
          span="4"
          style="color: #ccc; display: flex; align-items: center; justify-content: center"
        >
          {{ imgList.length }}/4
        </van-col>
      </van-row>
    </van-cell-group>

    <van-cell-group class="cell-group">
      <van-field
        :value="selectedSource"
        is-link
        readonly
        required
        label="需求来源"
        placeholder="请选择需求来源"
        :error-message="errors.source"
        input-align="right"
        @click="showSouerPopup"
      />
      <div class="dem_ts">
        发布需求平台赠送{{jf.value}}积分，需求将在每天10点、15点集中审核。为提高对接的成功率建议上传需求相关图片
      </div>
    </van-cell-group>

    <!-- 对接消耗积分 -->
    <!-- <van-cell-group class="cell-group">
      <van-cell
        clickable
        @click="form.is_orienteering = !form.is_orienteering"
      >
	   <template slot="title">
	      <span class="custom-title">定向对接（消耗 {{jf.value}} 积分）</span>
	    </template>
        <van-checkbox
          slot="icon"
          style="margin-right: 10px"
          v-model="form.is_orienteering"
          ref="checkboxes"
        />
      </van-cell>
    </van-cell-group> -->
    <van-button
      block
      square
      size="large"
      style="background-color: rgba(0,0,0,0.75); color: #fff; border: 0; margin-top: 20px"
    >
      <van-icon class-prefix="tj-icon" name="xiangmu" />
      <span style="font-size: 0.9em;">当前积分 {{ userInfo.integral }}</span>
    </van-button>
    <van-button block square size="large" type="danger" @click="onSubmit"
      >发布需求</van-button
    >

    <area-popup
      ref="areaPopup"
      :value="selectedAreaCode"
      @confirm="selectArea"
    />
    <demand-type-popup
      ref="demandTypePopup"
      :value="selectedDemandType"
      @confirm="selectDemandType"
    />

    <industry-popup
      ref="industryPopup"
      :value="selectedIndustry"
      @confirm="selectIndustry"
      @clear="clearIndustry"
    />
    <DemandSource
      :msg="showPicker"
      @val="sourceVal(arguments)"
      @cval="calVal"
      @cpop="cpopVal"
    ></DemandSource>
  </div>
</template>

<script>
import wx from "weixin-js-sdk";
import {
  NavBar,
  Button,
  Icon,
  Cell,
  CellGroup,
  Field,
  Uploader,
  Row,
  Col,
  ImagePreview,
  Checkbox
} from "vant";
import { mapGetters, mapState } from "vuex";
import { getImgUrlByMediaId } from "../../api/base";
import { createDemand, integral } from "../../api/demand";
import AreaPopup from "../../components/area-popup";
import DemandTypePopup from "../../components/demand-type-popup";
import IndustryPopup from "../../components/industry-popup";
import DemandSource from "../../components/demand-source";

export default {
  name: "create-demand",
  components: {
    [NavBar.name]: NavBar,
    [Button.name]: Button,
    [CellGroup.name]: CellGroup,
    [Cell.name]: Cell,
    [Field.name]: Field,
    [Uploader.name]: Uploader,
    [Row.name]: Row,
    [Col.name]: Col,
    [Icon.name]: Icon,
    [Checkbox.name]: Checkbox,
    AreaPopup,
    DemandTypePopup,
    IndustryPopup,
    DemandSource
  },
  data() {
    return {
      stud: null,
      demand_type: "",
      showPicker: "",
      selectedSource: "",
      sourceIndex: "",
      selectedArea: [],
      selectedDemandType: {},
      selectedIndustry: "",
      selectedIndustryObj: [],
      imgList: [],
      jf: [],
      form: {
        title: "", // 名称
        num: "", // 数量
        describe: "", // 描述
        is_orienteering: false, // 是否定向
        source: "" //来源
      },
      errors: {
        title: "",
        num: "",
        describe: "",
        is_orienteering: "",
        address: "",
        type: "",
        industry: "",
        img: "",
        source: "" //来源
      },
      mock: [
        { id: 1, name: "1k内" },
        { id: 2, name: "1-10k" },
        { id: 3, name: "10k以上" },
        { id: 4, name: "面议" }
      ]
    };
  },
  mounted() {
    integral({ key: "DirectionalDocking" }).then(res => {
      console.log(res);
      this.jf = res;
    });
  },
  watch: {},
  computed: {
    ...mapState(["from"]),
    ...mapGetters(["userInfo"]),
    selectedAreaCode() {
      const len = this.selectedArea.length;
      if (len > 0) {
        return this.selectedArea[len - 1].code;
      } else {
        return "440300";
      }
    },
    selectedAreaName() {
      const len = this.selectedArea.length;
      if (len > 0) {
        return this.selectedArea.map(item => item.name).join(" ");
        // return this.selectedArea[len - 1].name;
      } else {
        return "广东 深圳市";
      }
    }
  },
  methods: {
    actives(num, index) {
      this.stud = index;
      console.log(num, index, this.form.num);
      this.form.num = num.name;
      // form.num
    },
    // 点击返回按钮
    onClickLeft() {
      if (this.from) {
        this.$router.back();
      } else {
        this.$router.replace("/demands");
      }
    },

    // 打开地址选择器
    showAreaPopup() {
      this.$refs["areaPopup"].show();
    },

    // 打开需求类型选择器
    showDemandTypePopup() {
      this.$refs["demandTypePopup"].show();
    },

    // 打开需求行业选择器
    showIndustryPopup() {
      this.$refs["industryPopup"].show();
    },

    // 打开需求来源
    showSouerPopup() {
      this.showPicker = true;
    },
    //选择需求来源
    sourceVal(data) {
      let arr = Array.prototype.slice.call(data);
      this.selectedSource = arr[0];
      this.sourceIndex = arr[1];
      this.showPicker = arr[2];
      this.errors.source = "";
    },
    calVal(data) {
      console.log(data);
      this.showPicker = data;
    },
    cpopVal(data) {
      console.log(data);
      this.showPicker = data;
    },
    // 选择地址
    selectArea(val) {
      this.selectedArea = val;
      this.errors.address = "";
    },

    // 选择需求类型
    selectDemandType(val, index) {
      console.log(index);
      this.demand_type = index;
      this.selectedDemandType = val;
      this.errors.type = "";
    },

    // 选择需求行业
    selectIndustry(val) {
      const [col1, col2] = val;
      this.selectedIndustryObj = val;
      this.selectedIndustry = col1.name + "/" + col2.name;
      this.errors.industry = "";
    },

    clearIndustry() {
      this.selectedIndustry = "";
      this.errors.industry = "";
    },

    // 添加图片
    onUpload() {
      if (this.imgList.length < 4) {
        const count = 4 - this.imgList.length;
        wx.chooseImage({
          count, // 默认9
          sizeType: ["original", "compressed"], // 可以指定是原图还是压缩图，默认二者都有
          sourceType: ["album", "camera"], // 可以指定来源是相册还是相机，默认二者都有
          success: res => {
            const localIds = res.localIds; // 返回选定照片的本地ID列表，localId可以作为img标签的src属性显示图片
            this.syncUpload(localIds);
          }
        });
      } else {
        this.$toast("最多上传4张图片哦！");
      }
    },

    // 上传图片
    syncUpload(localIds) {
      const localId = localIds.pop();
      wx.uploadImage({
        localId: localId.toString(), // 需要上传的图片的本地ID，由chooseImage接口获得
        isShowProgressTips: 1, // 默认为1，显示进度提示
        success: res => {
          //res.serverId 返回图片的服务器端ID
          const serverId = res.serverId;

          this.getImgUrlByMediaId(serverId);

          if (localIds.length > 0) {
            window.setTimeout(() => {
              this.syncUpload(localIds);
            }, 100);
          } else {
            console.log(res);
          }
        }
      });
    },

    // 根据 mediaID 获取图片 url
    async getImgUrlByMediaId(mediaId) {
      const res = await getImgUrlByMediaId(mediaId);
      this.imgList.push(res);
    },

    // 预览图片
    previewImg(index) {
      const images = this.imgList.map(item => item.url);
      ImagePreview({
        images,
        startPosition: index,
        lazyLoad: true,
        onClose() {
          // do something
          console.log("close image preview");
        }
      });
    },
    // 删除图片
    delImg(index) {
      this.imgList.splice(index, 1);
    },

    // 提交表单
    async onSubmit() {
      const { title, describe, num, is_orienteering } = this.form;
      const img = this.imgList.map(item => item.url).join(",");
      const user_id = this.userInfo.id;
      const type = this.selectedDemandType.id;
      const industry = this.selectedIndustryObj[1]
        ? this.selectedIndustryObj[1].id
        : "0";
      const address = this.selectedAreaName;

      const source = this.sourceIndex + 1;
      console.log(source);

      if (!type) {
        this.errors.type = "请选择需求类型";
        return;
      }

      if (!industry) {
        this.errors.industry = "请选择行业";
        return;
      }

      if (!title) {
        this.errors.title = "请输入需求名称";
        return;
      }

      if (!address) {
        this.errors.address = "请选择需求地址";
        return;
      }

      if (!describe) {
        this.errors.describe = "请输入需求描述";
        return;
      }
      if (!this.selectedSource) {
        // console.log(this.source);
        this.errors.source = "请选择需求来源";
        return;
      }

      if (is_orienteering) {
		if(this.userInfo.is_vip == 1){
			console.log("vip")
		}else{
			if (this.userInfo.integral < 5) {
			  this.$dialog
			    .confirm({
			      title: "温馨提示",
			      message:
			        "当前积分不足，无法使用定向对接服务。请取消定向对接服务或充值后再发布",
			      confirmButtonText: "马上充值",
			      cancelButtonText: "重新发布"
			    })
			    .then(() => {
			      this.$router.push("/incharge");
			    })
			    .catch(() => {
			      // on cancel
			      this.form.is_orienteering = false;
			    });
			  return false;
			}
		}
		  
      }

      try {
        const demand = await createDemand({
          user_id,
          title,
          type,
          industry,
          address,
          source,
          describe: describe
            .replace(/\r\n/g, "<br/>")
            .replace(/\n/g, "<br/>")
            .replace(/\s/g, "&nbsp;"),
          img,
          num,
          is_orienteering: is_orienteering ? 1 : 0
        });

        this.$dialog
          .alert({
            title: "温馨提示",
            message: "发布成功，我们将尽快审核",
            confirmButtonText: "确定"
          })
          .then(() => {
            this.$router.replace(`/demands/${demand.id}`);
          });
      } catch (e) {
        this.$toast(e.message);
      }
    }
  }
};
</script>

<style lang="less">
.cell-group {
  margin-top: 12px;
}

.uploader {
  position: relative;
  width: 100%;
  padding-bottom: 100%;
  border: 1px solid #ccc;

  &::before,
  &::after {
    content: "";
    display: block;
    width: 70%;
    height: 2px;
    background-color: #ccc;
    position: absolute;
    top: 50%;
    left: 15%;
    margin-top: -1px;
  }

  &::after {
    transform: rotate(90deg);
  }
}

.imgDiv {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 100%;
  border: 1px solid #ccc;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.1);

  img {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    height: 100%;
    display: block;
  }

  i {
    position: absolute;
    font-size: 24px;
    top: -12px;
    right: -12px;
    z-index: 2;
  }
}
.dem_ts {
  margin: 2vw;
  font-size: 3vw;
  color: #666;
  padding: 2vw;
}
.text2 .van-field__control {
  min-height: 36vw !important;
}
.creatNum {
  display: flex;
  position: absolute;
  top: 3vw;
  right: 4vw;
  div {
    flex: 1;
    width: 15vw;
    // height: 6vw;
    border: 1px solid #999;
    border-radius: 10px;
    text-align: center;
    margin: 0 0.5vw;
    font-size: 3.5vw;
  }
}
.bechoice {
  background: #c11823;
  color: #fff;
  border: none;
}
@media (min-width: 750px) {
  .cell-group {
    margin-top: 12px;
  }

  .imgDiv {
    i {
      position: absolute;
      font-size: 24px;
      top: -12px;
      right: -12px;
      z-index: 2;
    }
  }
}
</style>
