import fetch from "../utils/fetch";

// 获取需求类型
export function fetchDemandTypes({ limit }) {
  return fetch({
    url: "/get_type",
    method: "get",
    params: {
      limit
    }
  });
}

// 获取需求分享二维码
export function demandShare(data) {
  return fetch({
    url: "/demand_share_qrcode",
    method: "get",
    params: data
  });
}

// 获取用户分享二维码
export function userdShare(data) {
  return fetch({
    url: "/user_share_qrcode",
    method: "get",
    params: data
  });
}

// 登录
export function loginByWXOC(data) {
  return fetch({
    url: "/user_info",
    method: "post",
    data
  });
}

