<template>
  <div class="create-request-page">
    <van-nav-bar
      fixed
      border
      title="修改需求"
      left-arrow
      @click-left="onClickLeft"
    />
    <van-cell-group class="cell-group">
      <van-field
        v-model="selectedDemandType.name"
        is-link
        readonly
        required
        label="需求类型"
        placeholder="请选择您的需求类型"
        :error-message="errors.type"
        input-align="right"
        error-message-align="right"
        @click="showDemandTypePopup"
      />
      <van-field
        v-model="form.title"
        required
        clearable
        label="需求名称"
        placeholder="请填写您的需求名称"
        :error-message="errors.title"
        input-align="right"
        error-message-align="right"
        @input="errors.title = ''"
      />
      <van-field
        v-model="form.num"
        clearable
        label="需求数量"
        placeholder="请填写您需求的数量"
        :error-message="errors.num"
        input-align="right"
        error-message-align="right"
      />
      <van-field
        :value="selectedAreaName"
        is-link
        readonly
        required
        label="需求地址"
        placeholder="请选择您的地址"
        input-align="right"
        error-message-align="right"
        :error-message="errors.address"
        @click="showAreaPopup"
      />
      <van-cell required title="需求描述" :border="false" />
      <van-field
	   ref="textVal"
        class="textarea"
        :value="form.describe"
        type="textarea"
        :error-message="errors.describe"
        @input="errors.describe = ''"
        placeholder="请具体描述一下您的需求，需求越具体效率越高哦"
      />
    </van-cell-group>
    <van-cell-group>
      <van-row style="padding: 10px 15px; display: flex">
        <van-col span="20">
          <van-row gutter="15">
            <van-col span="6" v-for="(item, index) in imgList" :key="index">
              <div class="imgDiv">
                <img :src="item.url" @click="previewImg(index)" />
                <van-icon name="close" @click="delImg(index)"></van-icon>
              </div>
            </van-col>
            <van-col span="6" v-if="imgList.length < 4">
              <div class="uploader" @click="onUpload"></div>
            </van-col>
          </van-row>
        </van-col>

        <van-col
          span="4"
          style="color: #ccc; display: flex; align-items: center; justify-content: center"
        >
          {{ imgList.length }}/4
        </van-col>
      </van-row>
    </van-cell-group>

    <van-cell-group class="cell-group">
      <van-field
        v-model="selectedSource"
        is-link
        readonly
        required
        label="需求来源"
        placeholder="请选择需求来源"
        :error-message="errors.source"
        input-align="right"
        @click="showSouerPopup"
      />
    </van-cell-group>
    <van-button block square size="large" type="danger" @click="onSubmit"
      >修改需求</van-button
    >

    <area-popup
      ref="areaPopup"
      :value="selectedAreaCode"
      @confirm="selectArea"
    />
    <demand-type-popup
      ref="demandTypePopup"
      :value="selectedDemandType"
      @confirm="selectDemandType"
    />

    <industry-popup
      ref="industryPopup"
      :value="selectedIndustry"
      @confirm="selectIndustry"
      @clear="clearIndustry"
    />
    <DemandSource
      :msg="showPicker"
      @val="sourceVal(arguments)"
      @cval="calVal"
      @cpop="cpopVal"
    ></DemandSource>
  </div>
</template>

<script>
import wx from "weixin-js-sdk";
import {
  NavBar,
  Button,
  Icon,
  Cell,
  CellGroup,
  Field,
  Uploader,
  Row,
  Col,
  ImagePreview,
  Checkbox
} from "vant";
import { mapGetters, mapState } from "vuex";
import { getImgUrlByMediaId, getUserInfo } from "../../api/base";
import { xgDemands, integral, getDemandById } from "../../api/demand";
import AreaPopup from "../../components/area-popup";
import DemandTypePopup from "../../components/demand-type-popup";
import IndustryPopup from "../../components/industry-popup";
import DemandSource from "../../components/demand-source";

export default {
  name: "create-demand",
  components: {
    [NavBar.name]: NavBar,
    [Button.name]: Button,
    [CellGroup.name]: CellGroup,
    [Cell.name]: Cell,
    [Field.name]: Field,
    [Uploader.name]: Uploader,
    [Row.name]: Row,
    [Col.name]: Col,
    [Icon.name]: Icon,
    [Checkbox.name]: Checkbox,
    AreaPopup,
    DemandTypePopup,
    IndustryPopup,
    DemandSource
  },
  data() {
    return {
      demand: {},
      xgDem: {},
      showPicker: "",
      selectedSource: "",
      sourceIndex: "",
      selectedArea: [],
      selectedDemandType: {},
      selectedIndustry: "",
      selectedIndustryObj: [],
      imgList: [],
      jf: [],
      form: {
        title: "", // 名称
        num: "", // 数量
        describe: "", // 描述
        is_orienteering: false, // 是否定向
        source: "" //来源
      },
      errors: {
        title: "",
        num: "",
        describe: "",
        is_orienteering: "",
        address: "",
        type: "",
        industry: "",
        img: "",
        source: "" //来源
      }
    };
  },
  async mounted() {
    integral({ key: "DirectionalDocking" }).then(res => {
      this.jf = res;
    });
    this.xgDem = await getUserInfo(this.userInfo.id);

    this.demand = await getDemandById({
      uid: this.userInfo.id,
      id: this.$route.query.uid
    });
    this.selectedSource =
      this.demand.source > "1" ? "代为发布需求" : "本公司需求";
    this.selectDemandType({ name: this.demand.typename, id: this.demand.type });
    Object.assign(this.form, this.demand);
	let b = this.form.describe.replace(/&nbsp;/g, "").replace(/<[^>]+>/g, "\r\n")
	this.$nextTick(()=>{
		 this.form.describe = b
	})
	
  },
  watch: {},
  computed: {
    ...mapState(["from"]),
    ...mapGetters(["userInfo"]),
    selectedAreaCode() {
      const len = this.selectedArea.length;
      if (len > 0) {
        return this.selectedArea[len - 1].code;
      } else {
        return "440300";
      }
    },
    selectedAreaName() {
      const len = this.selectedArea.length;
      if (len > 0) {
        return this.selectedArea.map(item => item.name).join(" ");
        // return this.selectedArea[len - 1].name;
      } else {
        return "广东 深圳市";
      }
    }
  },
  methods: {
    // 点击返回按钮
    onClickLeft() {
      if (this.from) {
        this.$router.back();
      } else {
        this.$router.replace("/demands");
      }
    },

    // 打开地址选择器
    showAreaPopup() {
      this.$refs["areaPopup"].show();
    },

    // 打开需求类型选择器
    showDemandTypePopup() {
      this.$refs["demandTypePopup"].show();
    },

    // 打开需求行业选择器
    showIndustryPopup() {
      this.$refs["industryPopup"].show();
    },

    // 打开需求来源
    showSouerPopup() {
      this.showPicker = true;
    },
    //选择需求来源
    sourceVal(data) {
      console.log(data);
      this.selectedSource = data[0];
      console.log(this.selectedSource);
      this.sourceIndex = data[1];
      this.showPicker = data[2];
      this.errors.source = "";
    },
    calVal(data) {
      console.log(data);
      this.showPicker = data;
    },
    cpopVal(data) {
      console.log(data);
      this.showPicker = data;
    },
    // 选择地址
    selectArea(val) {
      this.selectedArea = val;
      this.errors.address = "";
    },

    // 选择需求类型
    selectDemandType(val) {
      this.selectedDemandType = val;
      this.errors.type = "";
    },

    // 选择需求行业
    selectIndustry(val) {
      const [col1, col2] = val;
      this.selectedIndustryObj = val;
      this.selectedIndustry = col1.name + "/" + col2.name;
      this.errors.industry = "";
    },

    clearIndustry() {
      this.selectedIndustry = "";
      this.errors.industry = "";
    },

    // 添加图片
    onUpload() {
      if (this.imgList.length < 4) {
        const count = 4 - this.imgList.length;
        wx.chooseImage({
          count, // 默认9
          sizeType: ["original", "compressed"], // 可以指定是原图还是压缩图，默认二者都有
          sourceType: ["album", "camera"], // 可以指定来源是相册还是相机，默认二者都有
          success: res => {
            const localIds = res.localIds; // 返回选定照片的本地ID列表，localId可以作为img标签的src属性显示图片
            this.syncUpload(localIds);
          }
        });
      } else {
        this.$toast("最多上传4张图片哦！");
      }
    },

    // 上传图片
    syncUpload(localIds) {
      const localId = localIds.pop();
      wx.uploadImage({
        localId: localId.toString(), // 需要上传的图片的本地ID，由chooseImage接口获得
        isShowProgressTips: 1, // 默认为1，显示进度提示
        success: res => {
          //res.serverId 返回图片的服务器端ID
          const serverId = res.serverId;

          this.getImgUrlByMediaId(serverId);

          if (localIds.length > 0) {
            window.setTimeout(() => {
              this.syncUpload(localIds);
            }, 100);
          } else {
            console.log(res);
          }
        }
      });
    },

    // 根据 mediaID 获取图片 url
    async getImgUrlByMediaId(mediaId) {
      const res = await getImgUrlByMediaId(mediaId);
      this.imgList.push(res);
    },

    // 预览图片
    previewImg(index) {
      const images = this.imgList.map(item => item.url);
      ImagePreview({
        images,
        startPosition: index,
        lazyLoad: true,
        onClose() {
          // do something
          console.log("close image preview");
        }
      });
    },
    // 删除图片
    delImg(index) {
      this.imgList.splice(index, 1);
    },

    // 提交表单
    async onSubmit() {
      const { title, describe, num, is_orienteering } = this.form;
      const img = this.imgList.map(item => item.url).join(",");
      const user_id = this.userInfo.id;
      const type = this.selectedDemandType.id;
      const industry = this.selectedIndustryObj[1]
        ? this.selectedIndustryObj[1].id
        : "0";
      const address = this.selectedAreaName;

      const source = this.sourceIndex + 1;
      console.log(source);
      const demand_id = localStorage.getItem("demId");
      if (!type) {
        this.errors.type = "请选择需求类型";
        return;
      }

      if (!industry) {
        this.errors.industry = "请选择行业";
        return;
      }

      if (!title) {
        this.errors.title = "请输入需求名称";
        return;
      }

      if (!address) {
        this.errors.address = "请选择需求地址";
        return;
      }

      if (!describe) {
        this.errors.describe = "请输入需求描述";
        return;
      }
      if (!source) {
        // console.log(this.source);
        this.errors.source = "请选择需求来源";
        return;
      }

      try {
        const demand = await xgDemands({
          demand_id,
          user_id,
          title,
          type,
          industry,
          address,
          source,
          describe: describe
            .replace(/\r\n/g, "<br/>")
            .replace(/\n/g, "<br/>")
            .replace(/\s/g, "&nbsp;"),
          img,
          num,
          is_orienteering: is_orienteering ? 1 : 0
        });
        this.$dialog
          .alert({
            title: "温馨提示",
            message: "修改成功，我们将尽快审核",
            confirmButtonText: "确定"
          })
          .then(() => {
            this.$router.replace(`/demands/${demand_id}`);
          });
      } catch (e) {
        this.$toast(e.message);
      }
    }
  }
};
</script>

<style lang="less" scoped>
.cell-group {
  margin-top: 12px;
}

.uploader {
  position: relative;
  width: 100%;
  padding-bottom: 100%;
  border: 1px solid #ccc;

  &::before,
  &::after {
    content: "";
    display: block;
    width: 70%;
    height: 2px;
    background-color: #ccc;
    position: absolute;
    top: 50%;
    left: 15%;
    margin-top: -1px;
  }

  &::after {
    transform: rotate(90deg);
  }
}

.imgDiv {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 100%;
  border: 1px solid #ccc;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.1);

  img {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    height: 100%;
    display: block;
  }

  i {
    position: absolute;
    font-size: 24px;
    top: -12px;
    right: -12px;
    z-index: 2;
  }
}

@media (min-width: 750px) {
  .cell-group {
    margin-top: 12px;
  }

  .imgDiv {
    i {
      position: absolute;
      font-size: 24px;
      top: -12px;
      right: -12px;
      z-index: 2;
    }
  }
}
</style>
