<template>
  <div class="incharge-page" ref="mainContent" @scroll="handleContentScroll">
	  <!-- <tips/> -->
    <van-tabs sticky animated @change="onTabChange" v-model="activeTabIndex">
      <van-tab title="人脉广场">
        <div class="public-tab">
          <van-pull-refresh
            v-model="published.refreshing"
            @refresh="onPublishedRefresh"
          >
            <van-list
              style="min-height: 100vw;"
              v-model="published.loading"
              :finished="published.finished"
              finished-text="没有更多了"
              @load="loadMorePublished"
            >
              <user-card
                v-for="item in published.list"
                :key="item.id"
                :item="item"
              />
            </van-list>
          </van-pull-refresh>
        </div>
      </van-tab>
      <van-tab title="我的名片">
        <van-pull-refresh
          v-model="solved.refreshing"
          @refresh="onSolvedRefresh"
        >
          <van-list
            style="min-height: 100vw;"
            v-model="solved.loading"
            :finished="solved.finished"
            finished-text="没有更多了"
            @load="loadMoreSolved"
          >
            <user-card
              v-for="item in solved.list"
              :key="item.id"
              :item="item"
            />
          </van-list>
        </van-pull-refresh>
      </van-tab>
      <van-tab title="名片交换">
        <van-pull-refresh
          v-model="exchange.refreshing"
          @refresh="onExchangeRefresh"
        >
          <van-list
            style="min-height: 100vw;"
            v-model="exchange.loading"
            :finished="exchange.finished"
            finished-text="没有更多了"
            @load="loadMoreExchange"
          >
            <van-cell-group>
              <div
                class="user-item-wrap"
                v-for="item in exchangeTodo"
                :key="item.card_id"
              >
                <user-card :item="item" />
                <van-row
                  v-if="item.is_exchange === '0'"
                  gutter="14"
                  style="padding: 10px 15px; background: #fff"
                >
                  <van-col span="12">
                    <van-button
                      block
                      size="small"
                      @click="handleReject(item.card_id)"
                      >拒接交换</van-button
                    >
                  </van-col>
                  <van-col span="12">
                    <van-button
                      block
                      size="small"
                      type="danger"
                      @click="handleAgree(item.card_id)"
                      >同意交换</van-button
                    >
                  </van-col>
                </van-row>
              </div>
            </van-cell-group>

            <div v-if="exchangeDone.length > 0" class="exchange-done-title">
              - 以下为已处理的交换申请 -
            </div>
            <van-cell-group class="cell-group">
              <user-card
                v-for="item in exchangeDone"
                :key="item.id"
                :item="item"
                :show-status="true"
              />
            </van-cell-group>
          </van-list>
        </van-pull-refresh>
      </van-tab>
    </van-tabs>

    <div v-if="activeTabIndex === 0" class="toolbar van-hairline--bottom">
      <van-button
        class="supplier-type-btn"
        block
        size="small"
        @click="showIndustryPopup"
      >
        {{ selectedIndustryText || "全部行业" }}
        <van-icon name="arrow-down" class="icon" />
      </van-button>
      <form action="/" class="search-form">
        <van-search
          class="search"
          v-model="keyword"
          placeholder="搜索姓名/公司"
          @search="onPublishedRefresh"
        />
      </form>
    </div>

    <industry-popup
      ref="industryPopup"
      with-all
      :value="selectedIndustryText"
      @confirm="selectIndustry"
      @clear="clearIndustry"
    />
  </div>
</template>

<script>
import moment from "moment";
import { mapGetters } from "vuex";
import {
  Button,
  Row,
  Col,
  NavBar,
  Tag,
  List,
  Cell,
  CellGroup,
  PullRefresh,
  Tabs,
  Search,
  Icon,
  Tab,
  Image
} from "vant";
import {
  getSquare,
  getMyCards,
  getExchangeCards,
  updateCard
} from "../api/base";
import UserCard from "../components/user-card";
import IndustryPopup from "../components/industry-popup";
import tips from "@/components/tips";
export default {
  name: "about",
  components: {
    [Tabs.name]: Tabs,
    [Tab.name]: Tab,
    [Icon.name]: Icon,
    [Tag.name]: Tag,
    [Row.name]: Row,
    [Col.name]: Col,
    [CellGroup.name]: CellGroup,
    [List.name]: List,
    [Cell.name]: Cell,
    [Search.name]: Search,
    [PullRefresh.name]: PullRefresh,
    [NavBar.name]: NavBar,
    [Button.name]: Button,
	[Image.name]: Image,
    UserCard,
    IndustryPopup,
	tips
  },
  data() {
    return {
      publicPath: process.env.BASE_URL,
      moment,
      activeTabIndex: 0,
      keyword: "",
      selectedIndustry: [],
      published: {
        list: [],
        page: 0,
        limit: 20,
        loading: false,
        finished: false,
        refreshing: false
      },
      solved: {
        list: [],
        page: 0,
        limit: 20,
        loading: false,
        finished: false,
        refreshing: false
      },
      exchange: {
        list: [],
        page: 0,
        limit: 20,
        loading: false,
        finished: false,
        refreshing: false
      }
    };
  },
  mounted() {
    let initTabIndex = 0;
    switch (this.tabName) {
      case "square":
        initTabIndex = 0;
        break;
      case "my":
        initTabIndex = 1;
        break;
      case "exchange":
        initTabIndex = 2;
        break;
      default:
        break;
    }

    this.activeTabIndex = initTabIndex;
  },
  computed: {
    ...mapGetters(["userInfo"]),
    tabName() {
      return this.$route.query.tab || "square";
    },
    exchangeTodo() {
      return this.exchange.list.filter(item => item.is_exchange === "0");
    },
    exchangeDone() {
      return this.exchange.list.filter(item => item.is_exchange !== "0");
    },
    selectedIndustryText() {
      const [col1, col2] = this.selectedIndustry;
      let text = col1 ? col1.name : "";
      if (col2) {
        text = text + "/" + col2.name;
      }
      return text;
    }
  },
  watch: {
    selectedIndustry() {
      this.onPublishedRefresh();
    }
  },

  beforeRouteEnter(to, from, next) {
    next(vm => {
      console.log("beforeRouteEnter", vm.$route.meta);
      vm.$refs["mainContent"].scrollTop =
        vm.$route.meta[`y${vm.activeTabIndex}`] || 0;
    });
  },

  methods: {
    handleContentScroll(e) {
      // 节流；
      if (this.timerId) clearTimeout(this.timerId);
      this.timerId = setTimeout(() => {
        console.log(e.target.scrollTop);
        this.$route.meta[`y${this.activeTabIndex}`] = e.target.scrollTop;
      }, 300);
    },
    // 打开需求行业选择器
    showIndustryPopup() {
      this.$refs["industryPopup"].show();
    },
    // 选择需求行业
    selectIndustry(val) {
      this.selectedIndustry = val;
    },
    clearIndustry() {
      this.selectedIndustry = [];
    },

    // 点击返回按钮
    onTabChange(index) {
      let name = "square";
      switch (index) {
        case 0:
          name = "square";
          break;
        case 1:
          name = "my";
          break;
        case 2:
          name = "exchange";
          break;
        default:
          break;
      }

      this.$nextTick(() => {
        console.log(this.$route.meta[`y${index}`] || 0);
        this.$refs["mainContent"].scrollTop =
          this.$route.meta[`y${index}`] || 0;
      });

      this.$router.replace({ name: "connection-list", query: { tab: name } });
    },
    async onPublishedRefresh() {
      this.published.page = 1;
      const list = await this.fetchPublishedList();
      this.published.list = list;
      this.published.refreshing = false;
    },
    async loadMorePublished() {
      this.published.page = this.published.page + 1;
      const list = await this.fetchPublishedList();
      this.published.list = [...this.published.list, ...list];
      this.published.loading = false;
    },
    async fetchPublishedList() {
      const res = await getSquare({
        page: this.published.page,
        limit: this.published.limit,
        user_id: this.userInfo.id,
        industry_id: this.selectedIndustry[1]
          ? this.selectedIndustry[1].id
          : "",
        username: this.keyword
      });
      this.published.finished = res.length < this.published.limit;
      return res;
    },
    async onSolvedRefresh() {
      this.solved.page = 1;
      const list = await this.fetchSolvedList();
      this.solved.list = list;
      this.solved.refreshing = false;
    },
    async loadMoreSolved() {
      this.solved.page = this.solved.page + 1;
      const list = await this.fetchSolvedList();
      this.solved.list = [...this.solved.list, ...list];
      this.solved.loading = false;
    },
    async fetchSolvedList() {
      const res = await getMyCards({
        page: this.solved.page,
        limit: this.solved.limit,
        user_id: this.userInfo.id
      });
      this.solved.finished = res.length < this.solved.limit;
      return res;
    },
    async onExchangeRefresh() {
      this.exchange.page = 1;
      const list = await this.fetchExchangeList();
      this.exchange.list = list;
      this.exchange.refreshing = false;
    },
    async loadMoreExchange() {
      this.exchange.page = this.exchange.page + 1;
      const list = await this.fetchExchangeList();
      this.exchange.list = [...this.exchange.list, ...list];
      this.exchange.loading = false;
    },
    async fetchExchangeList() {
      const res = await getExchangeCards({
        page: this.exchange.page,
        limit: this.exchange.limit,
        user_id: this.userInfo.id
      });
      this.exchange.finished = res.length < this.exchange.limit;
      return res;
    },
    async handleReject(cardId) {
      await updateCard({
        card_id: cardId,
        type: "2"
      });

      const index = this.exchange.list.findIndex(
        item => item.card_id === cardId
      );
      this.exchange.list[index].is_exchange = "2";

      this.$dialog
        .alert({
          title: "温馨提示",
          message: "您已拒绝该交换请求",
          confirmButtonText: "确认"
        })
        .then(() => {});
    },
    async handleAgree(cardId) {
      await updateCard({
        card_id: cardId,
        type: "1"
      });

      const index = this.exchange.list.findIndex(
        item => item.card_id === cardId
      );
      this.exchange.list[index].is_exchange = "1";

      this.$dialog
        .alert({
          title: "温馨提示",
          message: "您已同意该交换请求",
          confirmButtonText: "确认"
        })
        .then(() => {});
    }
  }
};
</script>

<style lang="less" scoped>
.cell-group {
  margin-top: 12px;
}

.exchange-done-title {
  text-align: center;
  margin-top: 12px;
  font-size: 15px;
  color: #999;
}

.toolbar {
  position: fixed;
  top: 43px;
  left: 0;
  z-index: 1;
  width: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  padding:4px 15px;
  background-color: #fff;
}

.public-tab {
  padding-top: 50px;
}

.supplier-type-btn {
  width:150px;
  height: 34px;
  line-height: 34px;
  color: #999;
  background: #eeeeee;
  border-radius: 15px;
  font-size: 13px;
  overflow: hidden;
  text-align: left;
  padding: 0 15px 0 7px;

  .icon {
    position: absolute;
    right: 3px;
    top: 10px;
  }
}

.search {
  padding: 0 0 0 10px;
  width:150px;
  height: 34px;

  .van-search__content {
    background-color: #eee;
    border-radius: 15px;
    height: 100%;
    align-items: center;
  }
}

@media (min-width: 750px) {
  .cell-group {
    margin-top: 12px;
  }

  .exchange-done-title {
    margin-top: 12px;
    font-size: 15px;
  }

  .toolbar {
    top: 43px;
    left: 50%;
    margin-left: -375px;
    width: 750px;
    padding: 10px 15px;
  }

  .public-tab {
    padding-top: 50px;
  }

  .supplier-type-btn {
    width:150px;
    height: 34px;
    line-height: 34px;
    color: #999;
    background: #eeeeee;
    border-radius: 15px;
    font-size: 13px;
    overflow: hidden;
    text-align: left;
    padding: 0 15px 0 7px;
  
    .icon {
      position: absolute;
      right: 3px;
      top: 10px;
    }
  }
  

  .search {
    padding: 0 0 0 10px;
    width:150px;
    height: 34px;
  
    .van-search__content {
      background-color: #eee;
      border-radius: 15px;
      height: 100%;
      align-items: center;
    }
  }
}
</style>
