<template>
  <div class="demand-page">
    <van-panel class="demand">
      <!-- <van-cell
        class="demand-header"
        :title="moment.unix(demand.addtime).format('YYYY-MM-DD')"
        :value="`${demand.reason_data.length}人对接中`"
        slot="header"
      >
      </van-cell> -->
      <div class="demand-content" style="padding: 10px 15px;">
        <div class="title">
          {{ demand.typename }}| {{ demand.title }}
          <router-link
            :to="{ path: '/demands/xg', query: { uid: demand.id } }"
            v-if="demand.user_id == uids"
          >
            <span
              class="xg_span"
              v-if="demand.demand_status == 1 || demand.demand_status == 4"
              >修改需求></span
            >
          </router-link>
        </div>
        <div>需求量：{{ demand.num }}</div>
        <div>需求地址：{{ demand.address }}</div>
        <div>需求描述：</div>
        <div v-html="demand.describe"></div>
        <div>
          需求来源：{{ demand.source > "1" ? "代为发布需求" : "本公司需求" }}
        </div>
      </div>
      <van-row v-if="demand.images" gutter="15" slot="footer">
        <van-col span="6" v-for="(item, index) in demand.images" :key="index">
          <div class="imgDiv">
            <img :src="item" @click="previewImg(index)" />
          </div>
        </van-col>
      </van-row>
    </van-panel>

    <van-cell-group v-if="author" class="cell-group">
      <van-cell
        class="user-item"
        is-link
        :to="`/cards/${author.id}?demand_id=${demand.id}`"
      >
        <img class="avatar" :src="author.head_img" alt="" slot="icon" />
        <div class="title" slot="title">
          <span>{{ author.name }}</span>
          <span class="position">{{ author.position }}</span>
        </div>
        <div class="label" slot="label">
          {{ author.company }}
        </div>
        <div class="right" slot="right-icon">
          <van-icon name="arrow" />
        </div>
      </van-cell>
    </van-cell-group>

    <van-tabs class="cell-group" v-if="demand.user_id === userInfo.id">
      <!-- <van-tab title="需求申请者" v-if="demand.reason_data.length > 0">
        <van-list class="cell-group">
          <van-cell
            class="user-item"
            is-link
            v-for="item in demand.reason_data"
            :key="item.id"
            :to="`/cards/${item.user_id}?demand_id=${demand.id}`"
          >
            <img class="avatar" :src="item.head_img" alt="" slot="icon" />
            <div class="title" slot="title">
              <span>{{ item.name }}</span>
              <span class="position">{{ item.position }}</span>
              <span class="position">{{ item.company }}</span>
            </div>
            <div class="label" slot="label">{{ item.reason }}</div>
            <div class="right" slot="right-icon">
              <van-button
                size="small"
                v-if="item.status === '0'"
                plain
                round
                type="danger"
                @click="updateDemandRecord(item)"
                >待对接</van-button
              >
              <van-button
                size="small"
                v-if="item.status === '1'"
                round
                type="danger"
                >对接中</van-button
              >
              <van-button size="small" v-if="item.status === '2'" round disabled
                >未通过</van-button
              >
            </div>
          </van-cell>
        </van-list>
      </van-tab> -->
      <van-tab title="需求解决者" v-if="demand.demand_status === '3'">
        <van-list class="cell-group">
          <van-cell
            class="user-item"
            is-link
            v-if="demand.solve_user_id && demand.solve_user_id !== '0'"
            :to="
              `/cards/${demand.solve_user_data.id}?demand_id=${
                demand.solve_user_data.id
              }`
            "
          >
            <img
              class="avatar"
              :src="demand.solve_user_data.head_img"
              alt=""
              slot="icon"
            />
            <div class="title" slot="title">
              <span>{{ demand.solve_user_data.name }}</span>
              <span class="position">{{
                demand.solve_user_data.position
              }}</span>
              <span class="position">{{ demand.solve_user_data.company }}</span>
            </div>
            <div class="label" slot="label">
              {{ demand.solve_user_data.p_profile }}
            </div>
            <div class="right" slot="right-icon">
              <van-icon name="arrow" />
            </div>
          </van-cell>
          <van-cell
            v-if="
              (!demand.solve_user_id || demand.solve_user_id === '0') &&
                demand.channel_id
            "
          >
            <div style="color: #999; text-align: center">{{ channelName }}</div>
          </van-cell>
        </van-list>
      </van-tab>
    </van-tabs>

    <div
      class="demand-type-state info"
      v-if="demand.demand_status === '0' && demand.user_id === userInfo.id"
    >
      <h4>您的需求正在审核</h4>
      <p><span>您也可以联系我们：</span><span>18688720656</span></p>
    </div>

    <!-- <div
      class="demand-type-state warning"
      v-if="
        demand.demand_status === '1' &&
          demand.user_id !== userInfo.id &&
          demand.reason_data.find(
            item => item.user_id === userInfo.id && item.status !== '1'
          )
      "
    >
      <h4>您已申请，请等待需求方通过...</h4>
    </div> -->

    <div
      class="demand-type-state success"
      v-if="
        demand.demand_status === '2' &&
          demand.user_id !== userInfo.id &&
          !demand.reason_data.find(
            item => item.user_id === userInfo.id && item.status !== '1'
          )
      "
    >
      <h4>该需求已找到合作方，正在对接中...</h4>
    </div>

    <div
      class="demand-type-state disabled"
      v-if="
        demand.demand_status === '3' && demand.solve_user_id === userInfo.id
      "
    >
      <h4>该需求已由您解决</h4>
    </div>

    <div
      class="demand-type-state disabled"
      v-if="
        demand.demand_status === '3' && demand.solve_user_id !== userInfo.id
      "
    >
      <h4>该需求已解决/过期</h4>
    </div>

    <div
      class="demand-type-state error"
      v-if="demand.demand_status === '4' && demand.user_id === userInfo.id"
    >
      <h4>您的需求未能通过审核</h4>
      <p>请确保需求真实且描述清晰</p>
      <p><span>如需更多帮助请联系</span><span>18688720656</span></p>
    </div>
	  <van-button square type="danger" class="fiex"  block  v-if="this.userInfo.status != '2'" to="/my-card">注册</van-button>
    <van-row class="bottom-bar">
      <van-button square  @click="goBack" block>返回首页</van-button>
      <van-button
        square
        block
        type="danger"
        @click="solve"
        v-if="
          ['1', '2'].find(item => item === demand.demand_status) &&
            demand.user_id === userInfo.id
        "
        >结束需求</van-button
      >
      <!-- <van-button
        square
        block
        type="danger"
        @click="handleApply(demand)"
        v-if="
          demand.demand_status === '1' &&
            demand.user_id !== userInfo.id &&
            !demand.reason_data.find(item => item.user_id === userInfo.id)
        "
        >申请解决，联系对方</van-button
      > -->

      <van-button
        block
        square
        type="danger"
        :url="`tel:${author.mobile}`"
        v-if="
          author &&
            demand.demand_status === '1' &&
            demand.user_id !== userInfo.id &&
            demand.reason_data.find(
              item => item.user_id === userInfo.id && item.status === '1'
            )
        "
        >联系对方</van-button
      >
    </van-row>
  </div>
</template>
<script>
import QRCode from "qrcodejs2";
import html2canvas from "html2canvas";
import moment from "moment";
import { mapGetters, mapState, mapActions } from "vuex";
import {
  Button,
  Row,
  Col,
  CellGroup,
  Panel,
  Cell,
  PullRefresh,
  List,
  Icon,
  Tabs,
  Tab,
  ImagePreview
} from "vant";
import { getUserInfo } from "../../api/base";
import { updateDemandRecord } from "../../api/demand";

export default {
  name: "about",
  components: {
    [Row.name]: Row,
    [Col.name]: Col,
    [Button.name]: Button,
    [Cell.name]: Cell,
    [Icon.name]: Icon,
    [Tabs.name]: Tabs,
    [Tab.name]: Tab,
    [List.name]: List,
    [PullRefresh.name]: PullRefresh,
    [Panel.name]: Panel,
    [CellGroup.name]: CellGroup
  },
  props: ["demand", "status"],
  data() {
    return {
      uids: "",
      moment,
      author: null,
      applyedList: []
    };
  },
  computed: {
    ...mapState(["from"]),
    ...mapGetters(["userInfo", "channels"]),
    channelName() {
      const channel = this.channels.find(
        item => item.id === this.demand.channel_id
      );
      if (channel) {
        return channel.name;
      } else {
        return "已通过平台其他人脉解决";
      }
    },
    domandId() {
      return this.$route.params.id;
    }
  },
  watch: {
    demand(val) {
      this.getAuthor();
    }
  },
  async mounted() {
    console.log(this.userInfo.id);
    this.uids = this.userInfo.id;
    this.getAuthor();
    // if(this.status == true){
    // 	console.log(true)
    // 	// 生成海报
    // 	html2canvas(this.$refs.posterHtml,{
    // 		backgroundColor: null,
    // 		async: true,
    // 		  scale:.8
    // 	}).then(canvas => {
    // 		let dataURL = canvas.toDataURL("image/png");
    // 		this.posterImg = dataURL;
    // 		console.log(canvas)
    // 		if (this.posterImg !== "") {
    // 		  this.dialogTableVisible = true;
    // 		}
    // 	  });
    // }
  },
  methods: {
    ...mapActions(["fetchUserInfo"]),
    async getAuthor() {
      // 不是当前账号
      if (this.demand.user_id !== this.userInfo.id) {
        // 对接中 状态下 ，对接人是当前账号
        if (
          this.demand.demand_status === "1" &&
          this.demand.reason_data.find(
            item => item.user_id === this.userInfo.id && item.status === "1"
          )
        ) {
          this.author = await getUserInfo(this.demand.user_id);

          // 已结束 状态下，，对接人是当前账号
        } else if (
          this.demand.demand_status === "2" &&
          this.demand.solve_user_id === this.userInfo.id
        ) {
          this.author = await getUserInfo(this.demand.user_id);
        }
      } else {
        this.author = this.userInfo;
      }
    },
    // 点击返回按钮
    goBack() {
      if (this.from && this.from.name === "demand-list") {
        this.$router.back();
      } else {
        this.$router.replace("/demands");
      }
    },
    // 预览图片
    previewImg(index) {
      ImagePreview({
        images: this.demand.images,
        startPosition: index,
        lazyLoad: true,
        onClose() {
          // do something
          console.log("close image preview");
        }
      });
    },
    async updateDemandRecord(item) {
      await updateDemandRecord({
        user_id: this.userInfo.id,
        status: 1,
        id: item.id
      });

      this.$router.push(`/cards/${item.user_id}`);
    },
    async handleApply(demand) {
      try {
        await this.fetchUserInfo(this.userInfo.id);
        if (this.userInfo.is_black == 1) {
          this.$dialog
            .alert({
              title: "温馨提示",
              message: "您没有此权限，请联系平台管理员\n（电话：15602908103）",
              confirmButtonText: "确认"
            })
            .then(() => {
              console.log("confirm dialog");
            });
          return;
        }
        if (this.userInfo.status !== "2") {
          this.$dialog
            .confirm({
              title: "温馨提示",
              message: "您需要注册后才能进行该操作哦",
              confirmButtonText: "马上注册",
              cancelButtonText: "稍后再说"
            })
            .then(() => {
              if (this.userInfo.is_subscribe === "1") {
                this.$router.push("/my-card");
              } else {
                // this.$router.replace("/follow-us");
				console.log("未注册用户")
              }
            })
            .catch(() => {
              // on cancel
              console.log("cancel dialog");
            });
        } else if (this.userInfo.demand_num < 5) {
          this.$router.push(`/demands/${demand.id}/apply`);
        } else {
          this.$dialog
            .alert({
              title: "温馨提示",
              message:
                "您今天的申请次数已达上限,请明天再试。\n(每人每天最多可以申请5次)",
              confirmButtonText: "确认"
            })
            .then(() => {
              console.log("confirm dialog");
            });
        }
      } catch (error) {
        // todo: handle error
        this.$dialog
          .alert({
            title: "温馨提示",
            message: error.message || "申请失败",
            confirmButtonText: "确认"
          })
          .then(() => {
            console.log("confirm dialog");
          });
      }
    },
    solve() {
      this.$dialog
        .confirm({
          title: "温馨提示",
          message: "需求转为“结束”后，将不能接受他人需求申请",
          confirmButtonText: "确定",
          cancelButtonText: "取消"
        })
        .then(() => {
          this.$router.push(`/demands/${this.demand.id}/solve`);
        })
        .catch(() => {
          // on cancel
          console.log("cancel dialog");
        });
    }
  }
};
</script>

<style lang="less" scoped>
.cell-group {
  margin-top: 12px;
}
.fiex{
	position: fixed;
	bottom: 45px;
	 background: linear-gradient(90deg,#c40024,#d90a20);
	 border:1px solid #c40024 ;
}
.demand-page {
  padding-bottom: 45px;
}

.bottom-bar {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  display: flex;
}

.demand {
  margin-top: 12px;
}

.demand-header {
  font-size: 13px;
  color: #999;
}

.demand-content {
  padding: 10px 15px;
  color: #999;
  line-height: 1.75em;

  .title {
    font-size: 22px;
    line-height: 2em;
    color: #000;
  }
}

.imgDiv {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 100%;
  border: 1px solid #ccc;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.1);
  overflow: hidden;

  img {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    height: 100%;
    display: block;
  }
}

.user-item {
  .avatar {
    width: 42px;
    height: 42px;
    border-radius: 50%;
    border: 1px solid #ccc;
    margin-right: 10px;
  }

  .position {
    font-size: 12px;
    color: #999;
    margin-left: 10px;
  }

  .right {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.demand-type-state {
  margin-top: 12px;
  min-height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 15px 15px;
  text-align: center;
  color: #fff;

  h4,
  p {
    margin: 0;
    line-height: 1.75em;
  }

  h4 {
    font-size: 15px;
  }
  p {
    font-size: 13px;
    opacity: 0.75;
  }

  &.info {
    background: linear-gradient(to right, #0098de, #2ec5d2);
  }
  &.error {
    background: linear-gradient(to right, #c11823, #e63e22);
  }
  &.warning {
    background: linear-gradient(to right, #ff6e1d, #ff8f50);
  }
  &.success {
    background: linear-gradient(to right, #19a357, #00c08b);
  }
  &.disabled {
    // padding-top: 0px;
    // padding-bottom: 0px;
    // min-height: 50px;
    background: linear-gradient(to right, #aaaaaa, #dddddd);
  }
}

@media (min-width: 750px) {
  .cell-group {
    margin-top: 12px;
  }
.fiex{
	position: fixed;
	bottom: 45px;
}
  .bottom-bar {
    left: 50%;
    width: 750px;
    margin-left: -375px;
  }

  .demand-page {
    padding-bottom: 45px;
  }

  .demand {
    margin-top: 12px;
  }

  .demand-header {
    font-size: 13px;
  }

  .demand-content {
    padding: 10px 15px;

    .title {
      font-size: 22px;
    }
  }

  .user-item {
    .avatar {
      width: 42px;
      height: 42px;
      border: 1px solid #ccc;
      margin-right: 10px;
    }

    .position {
      font-size: 12px;
      margin-left: 10px;
    }
  }

  .demand-type-state {
    margin-top: 12px;
    min-height: 80px;
    padding: 15px 15px;

    h4 {
      font-size: 15px;
    }
    p {
      font-size: 13px;
    }
  }
}
.xg_span {
  float: right;
  color: #333;
  font-size: 1rem;
}
</style>
