<template>
  <div class="chat-page">
    <div class="yy" @click="hideYY" v-show="dialogTableVisible"></div>
    <div class="jt" v-show="dialogTableVisible">
      <img :src="posterImg" alt="" class="share_imgs" crossOrigin="anonymous" />
      <div class="HB_TS">
        长按上方图片发送给好友<br />成功邀请好友注册还有奖励哦！
      </div>
      <div class="close" @click="close">
        <img src="../../../public/img/close.png" alt="" />
      </div>
    </div>
    <div ref="posterHtml" class="HB_CLASS">
      <div class="poster_head">
        <img :src="userInfo.head_img" alt="" />
        <div class="HB_IFON">
          <h4>{{ userInfo.name }}</h4>
          <p>给你分享一个需求</p>
        </div>
      </div>
      <div class="demand-content brt" style="padding:8px 15px;height: 67vw;">
        <div class="title">
          <span>{{ demand.typename }}| {{ demand.title | titleElli}}</span>
        </div>
        <div class="demand_context">需求量：{{ demand.num }}</div>
        <div class="demand_context">需求地址：{{ demand.address }}</div>
        <div class="demand_context">需求描述：</div>
        <div class="demand_context whites" v-html="demand.describe"></div>
        <div class="fenCp">
          <img v-for="(item, index) in hbImg" :key="index" :src="item" alt="" />
        </div>
      </div>
      <div class="lianx">
        <div class="share_ewm">
          <img :src="share_ewm" alt="" />
		  <!-- <img src="../../../dist/img/tianjishe.jpg" alt=""> -->
        </div>
        <div class="hb_text">长按识别二维码<br />马上对接</div>
        <div class="share_logo">
          <img src="../../../public/share_logo.png" alt="" />
        </div>
      </div>
    </div>

    <div class="page-content" ref="page">
      <van-pull-refresh
        pulling-text="下拉加载更多历史..."
        loosing-text="释放加载更多历史..."
        loading-text="加载中..."
        v-model="refreshing"
        @refresh="onRefresh"
      >
        <van-list
          v-if="!onlyGroupOwner"
          ref="chatList"
          class="chat-list"
          v-model="loading"
          :finished="finished"
          finished-text="没有更多了"
          :immediate-check="false"
          @load="onLoad"
          :offset="100"
        >
          <div class="warning">
            <div>温馨提示:</div>
            <div>
              1、在线发言将自动成为该需求的对接人。成为对接人将消耗{{
                this.val
              }}积分。
            </div>
            <div>2、需求对接发言建议介绍公司优势及以往服务案例</div>
            <div>
              3、禁止在需求对接中刷屏、发布广告或其他无关需求的内容,违者将被拉黑处理
            </div>
          </div>

          <chat-item
            v-for="item in list"
            :key="item.record_id"
            :item="item"
            :isOnwer="item.user_id === userInfo.id"
            @toggle-audio="handleAudio"
            @preview-image="previewImg"
            @click-avatar="handleClickAvatar"
            :dataId="item.status"
          >
          </chat-item>
          <!-- v-show="item.status>0?item:'该需求已拉黑'" -->
          <!-- v-if="item.status>0" -->
        </van-list>
        <van-list
          v-else
          ref="chatList"
          class="chat-list"
          v-model="loading"
          :finished="finished"
          finished-text="没有更多了"
          :immediate-check="false"
          @load="onLoad"
          :offset="100"
        >
          <chat-item
            v-for="item in groupOwnerRecords"
            :key="item.record_id"
            :item="item"
            :isOnwer="item.user_id === userInfo.id"
            @toggle-audio="handleAudio"
            @preview-image="previewImg"
            @click-avatar="handleClickAvatar"
          />
        </van-list>
      </van-pull-refresh>
    </div>

    <div class="side-tools">
      <div class="item">
        <van-icon
          :class="['icon', onlyGroupOwner && 'active']"
          slot="label"
          name="zhikanxuqiuren"
          class-prefix="tj-icon"
          @click="onlyGroupOwner = !onlyGroupOwner"
        />
        <span>需求人</span>
      </div>
      <div class="item">
        <van-icon
          class="icon"
          slot="label"
          name="huidaoshouye"
          class-prefix="tj-icon"
          @click="handleGoHome"
        />
        <span>首页</span>
      </div>
      <div class="item">
        <!-- <van-icon class="icon" slot="label" name="share6" class-prefix="tj-icon" @click="fenxiang" /> -->
        <i
          style="font-size: 26px;"
          class="icon iconfont iconshare6"
          @click="fenxiang"
        ></i>
        <span class="red">分享<br/>赚积分</span>
      </div>
    </div>
    <!-- <van-row
      v-if="userInfo.is_black === '1'"
      class="bottom"
      style="padding: 0; display: block"
    >
      <van-button square @click="goToHome" block>返回首页</van-button>
    </van-row> -->

    <div
      class="bottom"
      v-if="
        demand.demand_status !== '3' &&
          userInfo.status === '2' &&
          userInfo.is_black !== '1'
      "
    >
      <VEmojiPicker
        v-show="visibleEmoji"
        class="emoji-box"
        :pack="pack.data"
        @select="selectEmoji"
      />

      <div class="bottom-bar">
        <van-icon
          class="icon"
          slot="label"
          name="yuyin"
          class-prefix="tj-icon"
          v-if="mode === 'text'"
          @click="mode = 'voice'"
        />
        <van-field
          class="message-field"
          placeholder="请输入内容"
          type="textarea"
          rows="1"
          autosize
          v-if="mode === 'text'"
          v-model="message"
          :clearable="false"
        >
        </van-field>
        <van-icon
          class="icon"
          slot="label"
          name="jianpan"
          class-prefix="tj-icon"
          v-if="mode === 'voice'"
          @click="mode = 'text'"
        />
        <button
          v-if="mode === 'voice'"
          class="voice-btn"
          size="small"
          block
          @touchstart="handleVoiceBtnTouchStart"
          @touchend="handleVoiceBtnTouchEnd"
        >
          按住说话
        </button>
        <!-- <van-icon
          @click="visibleEmoji = !visibleEmoji"
          class="icon smile"
          class-prefix="tj-icon"
          name="biaoqing"
        /> -->
        <van-button
          v-if="mode === 'text' && message"
          class="submit-btn"
          size="small"
          @click="sendText"
          >发送</van-button
        >
        <van-dropdown-menu v-else direction="up" class="action-dropdown">
          <van-dropdown-item
            title=""
            ref="actionDropdown"
            title-class="title-icon tj-icon tj-icon-tianjiawenjian"
          >
            <van-row>
              <van-col span="6">
                <action-icon
                  name="照片"
                  icon="picture"
                  icon-prefix="tj-icon"
                  @click="onUpload"
                />
              </van-col>
              <van-col span="6" style="text-align: center">
                <van-uploader :after-read="afterReadVideo" accept="video/*">
                  <action-icon name="视频" icon="video" icon-prefix="tj-icon" />
                </van-uploader>
              </van-col>

              <van-col span="6" style="text-align: center">
                <van-uploader :after-read="afterReadFile" accept="*">
                  <action-icon name="文件" icon="file" icon-prefix="tj-icon" />
                </van-uploader>
              </van-col>
              <van-col span="6" style="text-align: center">
                <action-icon
                  name="拍照"
                  icon="camera"
                  icon-prefix="tj-icon"
                  @click="onUpload"
                />
              </van-col>
            </van-row>
          </van-dropdown-item>
        </van-dropdown-menu>
      </div>
    </div>

    <van-row
      v-if="demand.demand_status === '3' && userInfo.is_black !== '1'"
      class="bottom"
      style="padding: 0; display: block"
    >
      <div class="demand-type-state disabled">
        <h4>该需求已解决/过期</h4>
      </div>
      <van-button square @click="goToHome" block>返回首页</van-button>
    </van-row>

    <van-row
      v-if="
        demand.demand_status !== '3' &&
          userInfo.status !== '2' &&
          userInfo.is_black !== '1'
      "
      class="bottom"
      style="padding: 0; display: block"
    >
      <div class="demand-type-state disabled">
        <h4>你尚未注册，无法在线沟通</h4>
      </div>
      <van-button square @click="goToRegister" block>马上注册</van-button>
    </van-row>
  </div>
</template>

<script>
import QRCode from "qrcodejs2";
import html2canvas from "html2canvas";
import wx from "weixin-js-sdk";
import { mapGetters, mapState } from "vuex";
import { setShare } from "@/wechat";
import { PAGE_BASE_URL } from "@/config";
import {
  Field,
  Button,
  Icon,
  List,
  Cell,
  Row,
  Col,
  Uploader,
  PullRefresh,
  DropdownMenu,
  ImagePreview,
  DropdownItem
} from "vant";
import VEmojiPicker from "v-emoji-picker";
import packData from "v-emoji-picker/data/emojis.json";
import { integral, demandShare } from "../../api/demand.js";
import ActionIcon from "../../components/action-icon";
import ChatItem from "../../components/chat-item";
import {
  getDemandChatList,
  sendChatText,
  sendChatImage,
  sendChatVoice,
  sendChatVideo,
  sendChatFile
} from "../../api/v3";
import { getDemandById } from "../../api/demand";
export default {
  name: "chat-page",
  props: ["demand"],
  components: {
    [Field.name]: Field,
    [Button.name]: Button,
    [List.name]: List,
    [Cell.name]: Cell,
    [Col.name]: Col,
    [Row.name]: Row,
    [Uploader.name]: Uploader,
    [PullRefresh.name]: PullRefresh,
    [DropdownMenu.name]: DropdownMenu,
    [DropdownItem.name]: DropdownItem,
    [Icon.name]: Icon,
    ActionIcon,
    ChatItem,
    VEmojiPicker
  },
  data() {
    return {
      share_ewm: "",
      showBG: false,
      mode: "text",
      message: "",
      visibleEmoji: false,
      voiceStartAt: undefined,
      firstRemoteRecordId: "",
      lastRemoteRecordId: "",
      onlyGroupOwner: false,
      list: [],
      refreshing: false,
      loading: false,
      finished: false,
      pack: packData,
      val: "",
      demandArr: [],
      posterImg: "",
      dialogTableVisible: false,
      Visible: false,
      hbImg: []
    };
  },
  computed: {
    ...mapState(["from"]),
    ...mapGetters(["userInfo"]),
    demandId() {
      return this.$route.params.id;
    },
    firstRecordId() {
      const length = this.list.length;
      return length > 0 ? this.list[0].record_id : "";
    },
    lastRecordId() {
      const length = this.list.length;
      return length > 0 ? this.list[length - 1].record_id : "";
    },
    imageRecords() {
      return this.list.filter(item => item.type === "2");
    },
    groupOwnerRecords() {
      return this.list.filter(item => item.is_group_owner);
    }
  },
  watch: {
    demandId() {
      this.mode = "text";
      this.message = "";
      this.firstRemoteRecordId = "";
      this.lastRemoteRecordId = "";
      this.list = [];
      this.refreshing = false;
      this.loading = false;
      this.finished = false;
      this.onLoad();
    }
  },
  async mounted() {
	// console.log(this.$route.params.id,123123)
    this.demandArr = await getDemandById({
      uid: this.userInfo.id,
      id: this.$route.params.id
    });
	console.log(this.demandArr)
	const desc = `${this.demand.typename}：${
	  this.demand.title
	}。点击查看需求详情`;
	const link = PAGE_BASE_URL + "#/demands/" + this.demand.id;
	setTimeout(() => {
	  setShare({
	    title: "天集社", // 分享标题
	    desc,
	    link
	  });
	}, 1000);
	
    demandShare({
      user_id: this.userInfo.id,
      demand_id: this.demandArr.id
    }).then(res => {
      this.share_ewm = res.url;
    });
    this.hbImg = this.demandArr.img.split(",");
    this.loadAll();
    wx.onVoiceRecordEnd({
      // 录音时间超过一分钟没有停止的时候会执行 complete 回调
      complete: res => this.handleRecordComplete(res)
    });
  },
  filters: {
      ellipsis(value) {
		  // console.log(value)
        if (!value) return "";
        if (value.length > 100) {
          return value.slice(0, 100) + "...";
        }
        return value;
      },
	  titleElli(value){
		  if (!value) return "";
		  if (value.length > 16) {
		    return value.slice(0, 16) + "...";
		  }
		  return value;
	  }
    },
  methods: {
    // 分享海报
    fenxiang() {
	if(this.userInfo.status == 2){
		//console.log(this.userInfo.status)
		if(this.demandArr.id){
				  // console.log(href?id=this.userInfo.id)
				  let user_id = this.userInfo.id;
				  // this.$emit("show",true)
				  // 生成海报
				  var copyDom = this.$refs.posterHtml; //要保存的dom
				  var width = copyDom.offsetWidth; //dom宽
				  var height = copyDom.offsetHeight; //dom高
				  var scale = 2; //放大倍数
				  html2canvas(copyDom, {
				    async: true,
				    useCORS: true,
				    allowTaint: true,
				    dpi: 326,
				    scale: 2,
				    taintTest: true
				  }).then(canvas => {
				    let dataURL = canvas
				      .toDataURL("image/png")
				      .replace("image/png", "image/octet-stream");
				    this.posterImg = dataURL;
				    if (this.posterImg !== "") {
				      this.dialogTableVisible = true;
				    }
				  });
			this.showBG = true;
		}
	}else{
		this.$dialog
		  .alert({
		    title: "温馨提示",
		    message: `请先注册`,
		    confirmButtonText: "确认"
		  }).then(res=>{
			  this.$router.push("/my-card")
		  })
	}
	  
    },
    hideYY() {
      this.dialogTableVisible = false;
    },
    close() {
      this.dialogTableVisible = false;
    },
    selectEmoji(emoji) {
      this.message += emoji.emoji;
    },
    async loadAll() {
      const { page_id, last_id, records } = await getDemandChatList({
        user_id: this.userInfo.id,
        demand_id: this.demandId,
        order: "asc",
        limit: 0
      });
	  //console.log(page_id, last_id, records )
      if (!this.firstRecordId) {
        this.list = this.sortList(records);
      } else {
        this.list = this.sortList(this.list, records);
      }

      // 加载状态结束
      this.loading = false;
      this.lastRemoteRecordId = last_id;
      this.finished = page_id === last_id;
    },
    async onLoad() {
      const { page_id, last_id, records } = await getDemandChatList({
        user_id: this.userInfo.id,
        demand_id: this.demandId,
        page_id: this.lastRecordId,
        order: "asc"
      });
      if (!this.firstRecordId) {
        this.list = this.sortList(records);
      } else {
        this.list = this.sortList(this.list, records);
      }

      // 加载状态结束
      this.loading = false;
      this.lastRemoteRecordId = last_id;
      this.finished = page_id === last_id;

      setTimeout(() => {
        //console.log();
        const page = this.$refs["page"];
        page.scrollTo({
          top: page.scrollHeight,
          left: 0,
          behavior: "smooth"
        });
      }, 200);
    },
    async onRefresh() {
      const { last_id, records } = await getDemandChatList({
        user_id: this.userInfo.id,
        demand_id: this.demandId,
        page_id: this.firstRecordId,
        order: "desc"
      });

      if (!this.firstRecordId) {
        this.list = this.sortList(records);
      } else {
        this.list = this.sortList(this.list, records);
      }

      // 加载状态结束
      this.refreshing = false;
      this.firstRemoteRecordId = last_id;
    },
    sortList(arr1, arr2 = []) {
      const obj = {};
      const cleanList = [];
      const handleFunc = item => {
        if (!obj[item.record_id]) {
          obj[item.record_id] = true;
          cleanList.push(item);
        }
      };
      arr1.forEach(handleFunc);
      arr2.forEach(handleFunc);
      return cleanList.sort((a, b) => a.record_id - b.record_id);
    },
    async sendText() {
      //console.log(this.demandArr.is_docking == "1");
      if (this.demandArr.is_docking == "1") {
        //console.log("已对接");
      } else {
        integral({
          key: "DockingDemand",
          type: 0
        }).then(res => {
			if(this.userInfo.is_vip == 1){
				//console.log("vip")
			}else{
				if (parseInt(this.userInfo.integral)<parseInt(res.value)) {
				  this.$dialog
				    .confirm({
				      title: "温馨提示",
				      message: "您目前积分余额不足，无法进行此操作。快去充值积分吧",
				      confirmButtonText: "确认"
				      // cancelButtonText: "重新发布"
				    })
				    .then(() => {
				      this.$router.push("/integral/recharge");
				    })
				    .catch(() => {
				      // on cancel
				      this.form.is_orienteering = false;
				    });
				  return false;
				}
			}
          
        });
      }
      const { message } = this;
      this.message = "";
      try {
        await sendChatText({
          user_id: this.userInfo.id,
          demand_id: this.demandId,
          content: message
        });
        this.onLoad();
      } catch (err) {
        // this.$toast.fail(err.message || err.statusText);
      }
    },
    // 添加图片
    onUpload() {
      this.$refs["actionDropdown"].toggle(false);
      wx.chooseImage({
        count: 9, // 默认9
        sizeType: ["original", "compressed"], // 可以指定是原图还是压缩图，默认二者都有
        sourceType: ["album", "camera"], // 可以指定来源是相册还是相机，默认二者都有
        success: res => {
          const localIds = res.localIds; // 返回选定照片的本地ID列表，localId可以作为img标签的src属性显示图片
          this.syncUpload(localIds);
        }
      });
      if (this.demandArr.is_docking == "1") {
        //console.log("本人对接需求不提弹窗");
      } else {
        integral({
          key: "DockingDemand",
          type: 0
        }).then(res => {
          if(this.userInfo.is_vip == 1){
          	//console.log("vip")
          }else{
          	if (parseInt(this.userInfo.integral)<parseInt(res.value)) {
          	  this.$dialog
          	    .confirm({
          	      title: "温馨提示",
          	      message: "您目前积分余额不足，无法进行此操作。快去充值积分吧",
          	      confirmButtonText: "确认"
          	      // cancelButtonText: "重新发布"
          	    })
          	    .then(() => {
          	      this.$router.push("/integral/recharge");
          	    })
          	    .catch(() => {
          	      // on cancel
          	      this.form.is_orienteering = false;
          	    });
          	  return false;
          	}
          }
        });
      }
    },

    // 上传图片
    syncUpload(localIds) {
      if (this.demandArr.is_docking == "1") {
        //console.log("本人对接需求不提弹窗");
      } else {
        integral({
          key: "DockingDemand",
          type: 0
        }).then(res => {
         if(this.userInfo.is_vip == 1){
         	//console.log("vip")
         }else{
         	if (parseInt(this.userInfo.integral)<parseInt(res.value)) {
         	  this.$dialog
         	    .confirm({
         	      title: "温馨提示",
         	      message: "您目前积分余额不足，无法进行此操作。快去充值积分吧",
         	      confirmButtonText: "确认"
         	      // cancelButtonText: "重新发布"
         	    })
         	    .then(() => {
         	      this.$router.push("/integral/recharge");
         	    })
         	    .catch(() => {
         	      // on cancel
         	      this.form.is_orienteering = false;
         	    });
         	  return false;
         	}
         }
        });
      }
      const localId = localIds.pop();
      wx.uploadImage({
        localId: localId.toString(), // 需要上传的图片的本地ID，由chooseImage接口获得
        isShowProgressTips: 1, // 默认为1，显示进度提示
        success: res => {
          //res.serverId 返回图片的服务器端ID
          const serverId = res.serverId;

          this.sendImage(serverId);

          if (localIds.length > 0) {
            window.setTimeout(() => {
              this.syncUpload(localIds);
            }, 100);
          } else {
            //console.log(res);
          }
        }
      });
    },

    // 根据 mediaID 获取图片 url
    async sendImage(media_id) {
      try {
        await sendChatImage({
          user_id: this.userInfo.id,
          demand_id: this.demandId,
          media_id
        });
        this.onLoad();
      } catch (error) {
        this.$toast.fail(error.message);
      }
    },

    handleClickAvatar(item) {
      this.$dialog
        .confirm({
          title: "温馨提示",
          message: `您即将前往 ${item.username} 的个人主页，是否继续?`,
          confirmButtonText: "继续",
          cancelButtonText: "稍后再说"
        })
        .then(() => {
          this.$router.push(`/cards/${item.user_id}`);
        })
        .catch(() => {
          // on cancel
          //console.log("cancel dialog");
        });
    },

    handleAudio() {},

    // 预览图片
    previewImg(recordId) {
      const images = this.imageRecords.map(item => item.url);
      const index = this.imageRecords.findIndex(
        item => item.record_id === recordId
      );
      ImagePreview({
        images,
        startPosition: index,
        lazyLoad: true,
        onClose() {
          // do something
          //console.log("close image preview");
        }
      });
    },

    handleVoiceBtnTouchStart(e) {
      if (this.demandArr.is_docking == "1") {
        //console.log("本人对接需求不提弹窗");
      } else {
        integral({
          key: "DockingDemand",
          type: 0
        }).then(res => {
          if(this.userInfo.is_vip == 1){
          	//console.log("vip")
          }else{
          	if (parseInt(this.userInfo.integral)<parseInt(res.value)) {
          	  this.$dialog
          	    .confirm({
          	      title: "温馨提示",
          	      message: "您目前积分余额不足，无法进行此操作。快去充值积分吧",
          	      confirmButtonText: "确认"
          	      // cancelButtonText: "重新发布"
          	    })
          	    .then(() => {
          	      this.$router.push("/integral/recharge");
          	    })
          	    .catch(() => {
          	      // on cancel
          	      this.form.is_orienteering = false;
          	    });
          	  return false;
          	}
          }
        });
        return false;
      }
      e.preventDefault();
      this.voiceStartAt = new Date();
      wx.startRecord({
        success: () => {
          this.$toast.loading({
            duration: 0, // 持续展示 toast
            forbidClick: true,
            loadingType: "spinner",
            message: "录音中"
          });
        },
        fail: res => {
          this.$toast.fail(res.message || "录音错误");
        }
      });
    },

    handleVoiceBtnTouchEnd() {
      const cur = new Date();
      if (cur - this.voiceStartAt < 1000) {
        this.$toast.fail("录音太短了");
        wx.stopRecord();
      } else {
        wx.stopRecord({
          success: res => this.handleRecordComplete(res)
        });
      }
    },

    handleRecordComplete(res) {
      this.$toast.clear();
      var localId = res.localId;
      wx.uploadVoice({
        localId, // 需要上传的音频的本地ID，由stopRecord接口获得
        isShowProgressTips: 1, // 默认为1，显示进度提示
        success: async res => {
          var serverId = res.serverId; // 返回音频的服务器端ID
          try {
            await sendChatVoice({
              user_id: this.userInfo.id,
              demand_id: this.demandId,
              media_id: serverId
            });
            this.onLoad();
          } catch (err) {
            this.$toast.fail(err.message);
          }
        }
      });
    },
    goToRegister() {
      if (this.userInfo.is_subscribe === "1") {
        this.$router.push("/my-card");
      } else {
        // this.$router.replace("/follow-us");
		//console.log("未注册用户")
      }
    },
    handleGoHome(e) {
      e.preventDefault();
      this.$dialog
        .confirm({
          title: "温馨提示",
          message: `您即将返回首页，是否继续?`,
          confirmButtonText: "继续",
          cancelButtonText: "稍后再说"
        })
        .then(() => {
          this.goToHome();
        })
        .catch(() => {
          // on cancel
          //console.log("cancel dialog");
        });
    },
    goToHome() {
      if (this.from && this.from.name === "demand-list") {
        this.$router.back();
      } else {
        this.$router.push({
          name: "demand-list"
        });
      }
    },
    async afterReadVideo({ file }) {
      if (this.demandArr.is_docking == "1") {
        //console.log("本人对接需求不提弹窗");
      } else {
        integral({
          key: "DockingDemand",
          type: 0
        }).then(res => {
          if(this.userInfo.is_vip == 1){
          	//console.log("vip")
          }else{
          	if (parseInt(this.userInfo.integral)<parseInt(res.value)) {
          	  this.$dialog
          	    .confirm({
          	      title: "温馨提示",
          	      message: "您目前积分余额不足，无法进行此操作。快去充值积分吧",
          	      confirmButtonText: "确认"
          	      // cancelButtonText: "重新发布"
          	    })
          	    .then(() => {
          	      this.$router.push("/integral/recharge");
          	    })
          	    .catch(() => {
          	      // on cancel
          	      this.form.is_orienteering = false;
          	    });
          	  return false;
          	}
          }
        });
      }
      this.$refs["actionDropdown"].toggle(false);
      const data = new FormData();
      data.append("user_id", this.userInfo.id);
      data.append("demand_id", this.demandId);
      data.append("video", file);
      const toast = this.$toast.loading({
        duration: 0,
        forbidClick: true,
        loadingType: "spinner",
        message: "上传中"
      });
      try {
        await sendChatVideo(data);
        toast.clear();
        this.onLoad();
      } catch (error) {
        toast.clear();
        this.$toast.fail(error.message);
      }
    },
    async afterReadFile({ file }) {
      if (this.demandArr.is_docking == "1") {
        //console.log("本人对接需求不提弹窗");
      } else {
        integral({
          key: "DockingDemand",
          type: 0
        }).then(res => {
         if(this.userInfo.is_vip == 1){
         	//console.log("vip")
         }else{
         	if (parseInt(this.userInfo.integral)<parseInt(res.value)) {
         	  this.$dialog
         	    .confirm({
         	      title: "温馨提示",
         	      message: "您目前积分余额不足，无法进行此操作。快去充值积分吧",
         	      confirmButtonText: "确认"
         	      // cancelButtonText: "重新发布"
         	    })
         	    .then(() => {
         	      this.$router.push("/integral/recharge");
         	    })
         	    .catch(() => {
         	      // on cancel
         	      this.form.is_orienteering = false;
         	    });
         	  return false;
         	}
         }
        });
      }
      this.$refs["actionDropdown"].toggle(false);
      const data = new FormData();
      data.append("user_id", this.userInfo.id);
      data.append("demand_id", this.demandId);
      data.append("file", file);
      const toast = this.$toast.loading({
        duration: 0,
        forbidClick: true,
        loadingType: "spinner",
        message: "上传中"
      });
      try {
        await sendChatFile(data);
        toast.clear();
        this.onLoad();
      } catch (error) {
        toast.clear();
        this.$toast.fail(error.message);
      }
    }
  }
};
</script>

