<template>
  <van-popup v-model="visible" position="bottom" :overlay="true">
    <van-picker
      show-toolbar
      title="选择供应商类型"
      value-key="name"
      :columns="supplierTypes"
      @cancel="onCancel"
      @confirm="onConfirm"
      :default-index="defaultIndex"
    />
  </van-popup>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import { Picker, Popup } from "vant";
import areaList from "../utils/area";

export default {
  name: "supplier-type-popup",
  components: {
    [Picker.name]: Picker,
    [Popup.name]: Popup
  },
  props: {
    confirm: Function,
    value: Object
  },
  data() {
    return {
      areaList,
      visible: false,
      loading: false
    };
  },
  computed: {
    ...mapGetters(["supplierTypes"]),
    defaultIndex() {
      return this.supplierTypes.findIndex(item => item.id === this.value.id);
    }
  },
  async mounted() {
    if (this.supplierTypes.length === 1) {
      this.loading = true;
      await this.fetchSupplierTypes({ limit: 1000 });
      this.loading = false;
    }
  },
  methods: {
    ...mapActions(["fetchSupplierTypes"]),
    show() {
      this.visible = true;
    },
    hide() {
      this.visible = false;
    },
    onCancel() {
      this.hide();
    },
    onConfirm(val) {
      console.log(val);
      this.$emit("confirm", val);
      this.hide();
    }
  }
};
</script>
