<template>
  <van-cell :to="`/cards/${item.id}`" class="user-item" is-link>
    <img class="avatar" v-if="item.head_img" :src="item.head_img" alt="" slot="icon" />
	<img class="avatar" v-else  src="http://demo.oramage.com/demo/tianji-vue-v3/img/loading-3.jpg" slot="icon" />
	<!-- <div>
		<van-image class="avatar" width="60" height="60" :src="item.head_img" fit="cover" >
				<template v-slot:error><img  width="60" height="60" src="http://demo.oramage.com/demo/tianji-vue-v3/img/loding.jpg" alt=""></template>
				<template v-slot:loading><img  width="60" height="60" src="http://demo.oramage.com/demo/tianji-vue-v3/img/loding.jpg" alt=""></template>
		 </van-image>
	</div> -->
    <div class="title" slot="title">
      <span>{{ item.name }}</span>
      <img
        class="level-icon"
        v-if="item.type === '1'"
        :src="`${publicPath}img/level-2.png`"
        alt=""
      />
      <span class="position">{{ item.position }}</span>
    </div>
    <div class="label" slot="label">
      <div>{{ item.company }}</div>
      <!-- <van-tag
        :class="['tag', getTagClass(index)]"
        v-for="(item, index) in item.attrs"
        :key="index"
        >{{ item }}</van-tag
      > -->
      <div class="tag-wapper" v-if="item.industry_names">
        <van-tag
          :class="['tag', getTagClass(index)]"
          v-for="(tag, index) in item.industry_names"
          :key="index"
          :color="index === 0 ? '#00c08b' : '#ff6e1d'"
          >{{ tag }}</van-tag
        >
        <van-tag
          :class="['tag', 'normal']"
          v-for="(tag, index) in formatAttrs(item.attrs)"
          :key="index"
          :color="index === 0 ? '#00c08b' : '#ff6e1d'"
          >{{ tag }}</van-tag
        >
      </div>

      <div class="tag-wapper" v-else>
        <van-tag
          :class="['tag', getTagClass(index)]"
          v-for="(tag, index) in formatAttrs(item.attrs)"
          :key="index"
          :color="index === 0 ? '#00c08b' : '#ff6e1d'"
          >{{ tag }}</van-tag
        >
      </div>
    </div>
    <div class="right" slot="right-icon">
      <div class="right-time">
        {{ moment.unix(item.addtime).format("YY-MM-DD HH:mm") }}
        <van-tag
          v-if="showStatus && item.is_exchange === '1'"
          class="tag"
          color="#c11823"
          >已同意</van-tag
        >
        <van-tag
          v-if="showStatus && item.is_exchange === '2'"
          class="tag"
          color="#999"
          >已拒绝</van-tag
        >
      </div>
      <div class="">{{ item.integral }}积分</div>
      <div class="">
        需求：{{ item.demand_count }} 解决：{{ item.solve_count }}
      </div>
    </div>
  </van-cell>
</template>

<script>
import moment from "moment";
import { Tag, Cell, Icon,Image} from "vant";
export default {
  name: "user-card",
  components: {
    [Icon.name]: Icon,
    [Tag.name]: Tag,
    [Cell.name]: Cell,
	[Image.name]: Image,
	
  },
  props: ["item", "showStatus"],
  data() {
    return {
      publicPath: process.env.BASE_URL,
      moment
    };
  },
  methods: {
    formatAttrs(attrs) {
      if (Array.isArray(attrs)) {
        return attrs;
      }
      const arr = attrs ? attrs.split("/") : [];
      return arr.filter(item => !!item && item !== "undefined");
    },
    getTagClass(index) {
      if (index === 0) return "first";
      if (index === 1) return "second";
      return "normal";
    }
  }
};
</script>

<style lang="less" scoped>
.user-item {
  .avatar {
    width: 60px;
    height: 60px;
    border-radius: 5px;
    border: 1px solid #ccc;
    background-color: #ccc;
    margin-right: 10px;
    margin-top: 6px;
  }

  .level-icon {
    width: 20px;
    vertical-align: middle;
    display: inline-block;
    margin-left: 7px;
  }

  .position {
    font-size: 12px;
    color: #999;
    margin-left: 10px;
  }

  .right {
    font-size: 12px;
    color: #999;
    text-align: right;
  }

  .tag {
    margin-top: 3px;
    margin-right: 7px;
  }

  .tag {
    &.first {
      background-color: #00c08b !important;
    }
    &.second {
      background-color: #ff6e1d !important;
    }
    &.normal {
      background-color: #fff !important;
      color: #000 !important;
      border: 1px solid blue !important;
    }
  }

  .right-time {
    .tag {
      margin-right: 0;
      margin-left: 7px;
      padding-top: 1px;
      padding-bottom: 1px;
    }
  }
}

.tag-wapper {
  width: 45vw;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@media (min-width: 750px) {
  .user-item {
    .avatar {
      width: 60px;
      height: 60px;
      border-radius: 5px;
      margin-right: 10px;
      margin-top: 6px;
    }

    .level-icon {
      width: 20px;
      margin-left: 7px;
    }

    .position {
      font-size: 12px;
      margin-left: 10px;
    }

    .right {
      font-size: 12px;
    }

    .tag {
      margin-top: 3px;
      margin-right: 7px;
    }

    .right-time {
      .tag {
        margin-left: 7px;
        padding-top: 1px;
        padding-bottom: 1px;
      }
    }
  }

  .tag-wapper {
    width: 520px;
  }
}
</style>
