<script>
	import moment from "moment";
	import {labelList} from "@/api/v4.js"
	import {
		mapActions,
		mapGetters
	} from "vuex";
	import {
		Button,
		Cell,
		CellGroup,
		Icon,
		Row,
		Col,
		Tag,
		Image,
		Popup,
		Checkbox,
		CheckboxGroup
	} from "vant";
	import html2canvas from "html2canvas";
	import {
		userdShare,
		loginByWXOC
	} from "../api/demand.js";
	export default {
		name: "user-center",
		components: {
			[CellGroup.name]: CellGroup,
			[Icon.name]: Icon,
			[Cell.name]: Cell,
			[Row.name]: Row,
			[Col.name]: Col,
			[Tag.name]: Tag,
			[Button.name]: Button,
			[Image.name]: Image,
			[Popup.name]: Popup,
			[Col.name]: Col,
			[Row.name]: Row,
			[Checkbox.name]: Checkbox,
			[CheckboxGroup.name]: CheckboxGroup,

		},
		data() {
			return {
				moment,
				share_ewm: "",
				showBG: false,
				showKF: false,
				dialogTableVisible: false,
				posterImg: "",
				Visible: false,
				showSubsribe: false,
				publicPath: process.env.BASE_URL,
				result: [],
				currIndex: [],
				arr: [],
			};
		},
		computed: {
			...mapGetters(["userInfo"])
		},
		async mounted() {
			this.fetchUserInfo(this.userInfo.id);
			userdShare({
				user_id: this.userInfo.id
			}).then(res => {
				this.share_ewm = res.url;
			});
			this.arr= await labelList();
		},
		methods: {
			...mapActions(["fetchUserInfo"]),
			getTagClass(index) {
				if (index === 0) return "first";
				if (index === 1) return "second";
				return "normal";
			},
			formatAttrs(attrs) {
				if (Array.isArray(attrs)) {
					return attrs;
				}
				const arr = attrs ? attrs.split("/") : [];
				return arr.filter(item => !!item && item !== "undefined");
			},
			// 分享海报
			fenxiang() {
				this.showBG = true;
				// console.log(href?id=this.userInfo.id)
				let user_id = this.userInfo.id;
				// this.$emit("show",true)
				// 生成海报
				var copyDom = this.$refs.posterHtml; //要保存的dom
				var width = copyDom.offsetWidth; //dom宽
				var height = copyDom.offsetHeight; //dom高
				var scale = 2; //放大倍数
				html2canvas(copyDom, {
					async: true,
					useCORS: true,
					allowTaint: true,
					dpi: 326,
					scale: 2,
					taintTest: true
				}).then(canvas => {
					let dataURL = canvas
						.toDataURL("image/png")
						.replace("image/png", "image/octet-stream");
					this.posterImg = dataURL;
					if (this.posterImg !== "") {
						this.dialogTableVisible = true;
					}
				});
			},
			hideYY() {
				this.dialogTableVisible = false;
			},
			close() {
				this.dialogTableVisible = false;
			},
			edit() {
				this.$router.push("/my-card")
			},
			gotoRecords() {
				this.$router.push("/records")
			},
			gotointegral() {
				this.$router.push("/integral/recharge")
			},
			gotoDock() {
				this.$router.push("/dock")
			},
			goIntail() {
				this.$router.push("/integral/index")
			},
			showGL() {
				// this.$dialog.alert({
				// 	title:"温馨提示",
				// 	message:"如需帮助，请拨打平台管理员电话<br/>15602908103"
				// })
				this.showKF = true
			},
			hdchange(val) {
				console.log(this.result)
			}
		}
	};
</script>

