<template>
  <div class="demand-layout">
    <van-tabs
      class="cus-tabs"
      sticky
      @change="onTabChange"
      v-model="activeTabName"
    >
      <van-tab name="detail" title="需求详情" />
      <van-tab name="chat" :disabled="this.userInfo.status != 2?true:false" title="在线对接"/>
      <!-- <van-tab name="butts" :disabled="!canVisitButts" title="对接名单" /> -->
	  <van-tab name="butts" :disabled="this.userInfo.status != 2?true:false" title="对接名单" />
	   <!-- :disabled="this.userInfo.status != 2?true:false" -->
    </van-tabs>

    <div class="demand-layout-content">
      <keep-alive>
        <demand-detail
          :demand="demand"
          :status="status"
          class="demand-tab-item-page"
          v-if="activeTabName === 'detail'"
        />
        <demand-chat
          :demand="demand"
          class="demand-tab-item-page"
          v-if="activeTabName === 'chat'"
          @show="show"
        />
        <demand-butts
          :demand="demand"
          class="demand-tab-item-page"
          v-if="activeTabName === 'butts'"
        />
      </keep-alive>
    </div>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
import { Tab, Tabs } from "vant";
import DemandDetail from "./demand/detail";
import DemandChat from "./demand/chat";
import DemandButts from "./demand/butts";
import { setShare } from "../wechat";
import { getDemandById } from "../api/demand";
import { PAGE_BASE_URL } from "../config";
import { getUserInfo} from "@/api/base.js";
import axios from "axios";

export default {
  name: "TabbarLayout",
  components: {
    [Tab.name]: Tab,
    [Tabs.name]: Tabs,
    DemandDetail,
    DemandChat,
    DemandButts
  },
  data() {
    return {
      canVisitButts: true,
      activeTabName: "chat",
      dstatus: "",
      demd: {},
      demand: {
        reason_data: []
      },
      status: ""
    };
  },
  computed: {
    ...mapGetters(["userInfo"]),
    demandId() {
      return this.$route.params.id;
    },
    tabName() {
      return this.$route.query.tab || "chat";
    }
  },
  async mounted() {
	  getUserInfo(sessionStorage.getItem("tianji-v2-z-userid")).then(res=>{
	  	if (res.status != 2) {
	  		console.log(this.userInfo.status)
	  	  this.activeTabName = "detail"
	  	}else{
	  		this.activeTabName = "chat"
	  	}
	  })
    
    await this.fetchDemand();
    localStorage.setItem("demId", this.$route.params.id);
  },
  watch: {
    tabName(name) {
      this.activeTabName = name;
    },
    domandId() {
      this.fetchDemand();
    }
  },
  methods: {
    show(data) {
      this.status = data;
      console.log(data);
    },
    onTabChange(name) {
      this.$router.replace({ name: "demand-detail", query: { tab: name } });
    },
    async fetchDemand() {
      const demand = await getDemandById({
        uid: this.userInfo.id,
        id: this.$route.params.id
      });
      const images = demand.img ? demand.img.split(",") : [];

      this.demand = { ...demand, images };
      this.dstatus = this.demand.status;

      if (this.demand.demand_status === "3" ) {
        this.canVisitButts = this.demand.user_id === this.userInfo.id;
        if (this.tabName === "butts" && !this.canVisitButts) {
          this.onTabChange("detail");
          this.activeTabName = "detail";
        }
      } else if (this.demand.demand_status === "5") {
        console.log(111);
      } else {
        this.canVisitButts = true;
      }
	 // if(this.userInfo.status == 2){
		//  this.canVisitButts = false;
		//  console.log('zhix')
	 // }
      const desc = `${this.demand.typename}：${
        this.demand.title
      }。点击查看需求详情`;
      const link = PAGE_BASE_URL + "#/demands/" + this.demand.id;
      setTimeout(() => {
        setShare({
          title: "天集社", // 分享标题
          desc,
          link
        });
      }, 1000);
    }
  }
};
</script>
<style lang="less" scoped>
.demand-layout {
  display: flex;
  flex-direction: column;
  padding-top: 0;
  padding-bottom: 0;
  box-sizing: border-box;
}

.demand-layout-content {
  flex: 1;
  overflow: hidden;
  position: relative;
}

.demand-layout-page {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow-y: auto;
}

.demand-tab-item-page {
  position: absolute;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  overflow-y: auto;
}

@media (min-width: 750px) {
  .demand-layout {
    padding-top: 0;
    padding-bottom: 0;
  }
}
</style>

<style lang="less">
.demand-layout .cus-tabs {
  .van-tab {
    font-weight: bolder;
  }
}
</style>
