
import Vue from "vue";
import qs from "qs";
import 'vant/lib/index.css';
import { Toast, Dialog, Lazyload, Icon } from "vant";
import MtaH5 from "mta-h5-analysis";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import { redirectOCAuth, loginByWXOC } from "./api/base";
Vue.use(Toast)
  .use(Icon)
  .use(Dialog)
  .use(Lazyload,{
	  loading:require("../public/img/loading-3.jpg"),
	  error:require("../public/img/loading-3.jpg"),
  });

Vue.config.productionTip = false;

// prod env do not load vconsole
if (process.env.NODE_ENV !== "production") {
  // const VConsole = require("vconsole");
  // window.vConsole = new VConsole();
} else {
  // 初始化
  MtaH5.init({
    sid: "500668155", //必填，统计用的appid
    // cid: "********", //如果开启自定义事件，此项目为必填，否则不填
    autoReport: 0, //是否开启自动上报(1:init完成则上报一次,0:使用pgv方法才上报)
    senseHash: 1, //hash锚点是否进入url统计
    senseQuery: 1, //url参数是否进入url统计
    performanceMonitor: 1, //是否开启性能监控
    ignoreParams: ["token", "v", "from"] //开启url参数上报时，可忽略部分参数拼接上报
  });

  // const VConsole = require("vconsole");
  // window.vConsole = new VConsole();
}
let search = qs.parse(window.location.search);
// 路由中没有 code 参数
if (!search || !search.code) {
  //测试userid
  // sessionStorage.setItem("tianji-v2-z-userid", "1571"); //1530 6590 14575
  const userId = sessionStorage.getItem("tianji-v2-z-userid");
  // 本地没有缓存用户 ID
  if (!userId) {
    redirectOCAuth();
  } else {
    new Vue({
      router,
      store,
      render: h => h(App)
    }).$mount("#app");
  }
} else {
  const { code } = search;
  let { user_id: inviter } = search;
  if (!inviter) {
    const hashSearch = qs.parse(window.location.hash.split("?")[1]);
    if (hashSearch) {
      inviter = hashSearch.user_id;
    }
  }
  sessionStorage.setItem("inviter",inviter)
  sessionStorage.setItem("code",code)
  loginByWXOC(code, inviter).then(res => {
	  console.log(res)
    if (res.is_subscribe == 0) {
      router.replace("/follow-us");
    }
	sessionStorage.setItem("token",res.token)
    sessionStorage.setItem("tianji-v2-z-userid", res.id);
    window.location.search = "";
  });
}
