<template>
  <div class="supplier-list-page">
	<!-- <tips/> -->
    <div class="toolbar van-hairline--bottom">
      <van-button
        class="supplier-type-btn"
        block
        size="small"
        @click="showIndustryPopup"
      >
        {{ selectedIndustryText || "全部行业" }}
        <van-icon name="arrow-down" class="icon" />
      </van-button>
      <form action="/" class="search-form">
        <van-search
          class="search"
          v-model="keyword"
          placeholder="搜索公司"
          @search="onSearch"
        />
      </form>
    </div>
    <div class="main-content" ref="mainContent" @scroll="handleContentScroll">
      <van-pull-refresh
        v-model="refreshing"
        @refresh="onRefresh"
        class="refresh-content"
      >
        <van-list
          v-model="loading"
          :finished="finished"
          finished-text="没有更多了"
          @load="loadMore"
        >
          <supplier-item
            v-for="item in list"
            :key="item.supplier_id"
            @click="handleSupplierClick(item.supplier_id)"
            :item="item"
          />
        </van-list>

        <div class="no-content" v-if="list.length === 0">
          <img src="../../assets/no-content-color.png" alt="" />
          <p><strong>暂无供应商</strong></p>
          <p>~当前类型暂无供应商~</p>
        </div>
      </van-pull-refresh>
    </div>

    <!-- <supplier-type-popup
      ref="supplierTypePopup"
    /> -->
	<van-button
	   slot="action"
	   block
	   size="small"
	   type="danger"
	   class="request-btn"
	   @click="showTled"
	   >申请公司入驻</van-button
	 >
	 <van-dialog v-model="show" @confirm="confirms" show-cancel-button cancelButtonText="稍后再说" confirmButtonText="已签订协议,去认证">
		 <p style="text-align: center;">公司入驻提示</p> 
		 <p class="tisp_p">公司入驻需和平台签订入驻协议,可下载电子版协议,入驻人填写信息加盖企业公章后,上传审核</p>
		 <van-button @click="download" hairline  type="danger" class="teld_btn btnColor" >下载入驻协议</van-button>
	 </van-dialog>
	 
	 <industry-popup
	   ref="industryPopup"
	   with-all
	   :value="selectedIndustryText"
	   @confirm="selectIndustry"
	   @clear="clearIndustry"
	 />
  </div>
</template>
<script>
import { mapActions, mapGetters } from "vuex";
import {
  Button,
  Swipe,
  SwipeItem,
  Row,
  Col,
  NavBar,
  List,
  Cell,
  Icon,
  PullRefresh,
  Search
} from "vant";
import { getSupplierList } from "../../api/v3";
import { companyFile } from "@/api/v4";
import SupplierItem from "../../components/supplier-item";
import SupplierTypePopup from "../../components/supplier-type-popup";
import tips from "@/components/tips";
import IndustryPopup from "@/components/industry-popup";
export default {
  name: "suppliers",
  components: {
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
    [Row.name]: Row,
    [Col.name]: Col,
    [List.name]: List,
    [Icon.name]: Icon,
    [Cell.name]: Cell,
    [PullRefresh.name]: PullRefresh,
    [NavBar.name]: NavBar,
    [Button.name]: Button,
    [Search.name]: Search,
    SupplierItem,
    SupplierTypePopup,
	tips,
	IndustryPopup,
  },
  data() {
    return {
      timerId: null,
      keyword: "",
      company: "",
	  selectedIndustry: [],
      list: [],
      page: 0,
      limit: 20,
      loading: false,
      finished: false,
      refreshing: false,
	  show:false,
	  industry_id:null
    };
  },
  computed: {
    ...mapGetters(["userInfo", "supplierTypes"]),
	selectedIndustryText() {
	  const [col1, col2] = this.selectedIndustry;
	  let text = col1 ? col1.name : "";
	  if (col2) {
	    text = text + "/" + col2.name;
	  }
	  return text;
	}
  },
  watch: {
    selectedIndustry() {
      this.onRefresh();
    }
  },
  async mounted() {
    // this.onRefresh();
    // this.selectedIndustry = this.supplierTypes[0];
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      console.log("beforeRouteEnter", vm.$route.meta.y);
      vm.$refs["mainContent"].scrollTop = vm.$route.meta.y;
    });
  },
  methods: {
    ...mapActions(["fetchUserInfo"]),
	// 处理“发布需求”按钮点击事件
	showTled(){
		this.show = true
	},
	download(){
		companyFile().then(res=>{
			console.log(res)
			window.location.href = res.url
		})
	},
	confirms(){
		this.$router.push("/settled")
	},
	async handleCreateDemand() {
	  await this.fetchUserInfo(this.userInfo.id);
	  if (this.userInfo.is_black == 1) {
	    this.$dialog
	      .alert({
	        title: "温馨提示",
	        message: "您没有此权限，请联系平台管理员\n（电话：15602908103）",
	        confirmButtonText: "确认"
	      })
	      .then(() => {
	        console.log("confirm dialog");
	      });
	    return;
	  }
	  if (this.userInfo.status !== "2") {
	    this.$dialog
	      .confirm({
	        title: "温馨提示",
	        message: "您需要注册后才能进行该操作哦",
	        confirmButtonText: "马上注册",
	        cancelButtonText: "稍后再说"
	      })
	      .then(() => {
	        if (this.userInfo.is_subscribe === "1") {
	          this.$router.push("/my-card");
	        } else {
	          // this.$router.replace("/follow-us");
			  console.log("未关注用户")
	        }
	      })
	      .catch(() => {
	        // on cancel
	        console.log("cancel dialog");
	      });
	  } else {
	    if (this.userInfo.is_subscribe === "1") {
	      this.$router.push("/demands/new");
	    } else {
	      // this.$router.replace("/follow-us");
		  console.log("未关注用户")
	    }
	  }
	},
    handleContentScroll(e) {
      // 节流；
      if (this.timerId) clearTimeout(this.timerId);
      this.timerId = setTimeout(() => {
        this.$route.meta.y = e.target.scrollTop;
      }, 300);
    },

    async onRefresh() {
      this.$refs["mainContent"].scrollTop = 0;
      this.page = 1;
      const list = await this.onLoad();
      this.list = list;
      this.refreshing = false;
    },
    async loadMore() {
      this.page = this.page + 1;
      const list = await this.onLoad();
      this.list = [...this.list, ...list];
      this.loading = false;
    },
    async onLoad() {
      // 异步更新数据
      const res = await getSupplierList({
        industry_id: this.selectedIndustry[1]
          ? this.selectedIndustry[1].id
          : "",
        company: this.company,
        page: this.page,
        pagesize: this.limit
      });

      this.finished = res.length < this.limit;
      return res;
    },
    async handleSupplierClick(id) {
      await this.fetchUserInfo(this.userInfo.id);
      if (this.userInfo.status !== "2") {
        this.$dialog
          .confirm({
            title: "温馨提示",
            message: "您需要注册后才能进行该操作哦",
            confirmButtonText: "马上注册",
            cancelButtonText: "稍后再说"
          })
          .then(() => {
            if (this.userInfo.is_subscribe === "1") {
              this.$router.push("/my-card");
            } else {
              // this.$router.replace("/follow-us");
			  console.log("未注册用户")
            }
          })
          .catch(() => {
            // on cancel
            console.log("cancel dialog");
          });
      } else {
        if (this.userInfo.is_subscribe === "1") {
          // await addCountForsupplier(id);
          this.$router.push(`/suppliers/${id}`);
        } else {
          // this.$router.replace("/follow-us");
		  console.log("未注册用户")
        }
      }
    },
    onSearch(val) {
      this.company = val;
      this.onRefresh();
    },
	// 打开需求行业选择器
	showIndustryPopup() {
	  this.$refs["industryPopup"].show();
	},
	// 选择需求行业
	selectIndustry(val) {
	  this.selectedIndustry = val;
	},
	clearIndustry() {
	  this.selectedIndustry = [];
	},
  }
};
</script>

<style lang="less" scoped>
.btnColor{
	background:linear-gradient(90deg,rgba(196,0,36,1),rgba(217,10,32,1));
}	
.supplier-list-page {
  display: flex;
  flex-direction: column;
}
.request-btn {
	width:122px;
	height:35px;
	background:linear-gradient(90deg,rgba(196,0,36,1),rgba(217,10,32,1));
	box-shadow:0px 1px 2px 2px rgba(194,21,67,0.3);
	border-radius:17px;
	  font-size: 16px;
	  border: none;
	  position: fixed;
	  bottom:80px;
	  left: 50%;
	  transform: translateX(-50%);
	  z-index: 1;
}
.main-content {
  flex: 1;
  overflow-y: auto;
}

.refresh-content {
  min-height: 100%;
}

.toolbar {
  width: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  padding: 10px 15px;
  background-color: #fff;
}

.supplier-type-btn {
  width: 150px;
  height: 34px;
  line-height: 34px;
  color: #999;
  background: #eeeeee;
  border-radius:15px;
  font-size: 13px;
  overflow: hidden;
  text-align: left;
  padding: 0 15px 0 7px;

  .icon {
    position: absolute;
    right: 3px;
    top: 10px;
  }
}

.search {
  padding: 0 0 0 10px;
  width: 150px;
  height: 34px;

  .van-search__content {
    background-color: #eee;
    border-radius:15px;
    height: 100%;
    align-items: center;
  }
}

.no-content {
  flex: 1;
  flex-direction: column;
  background-color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 50px 20px;

  p {
    font-size: 14px;
    margin: 0;
    color: #999;
    line-height: 2em;
    strong {
      color: #000;
      font-size: 1.2em;
    }
  }

  img {
    width: 120px;
    margin-bottom: 30px;
  }
}
.tisp_p{
	width: 90%;
	text-align: center;
	margin:20px auto;
}
.teld_btn{
	display: block;
	margin:30px auto;
}
@media (min-width: 750px) {
	.tisp_p{
		width: 90%;
		text-align: center;
		margin:20px auto;
	}
	.teld_btn{
		display: block;
		margin:30px auto;
	}
  .supplier-list-page {
    display: flex;
    flex-direction: column;
  }

  .main-content {
    flex: 1;
    overflow-y: auto;
  }

  .refresh-content {
    min-height: 100%;
  }

  .toolbar {
    width: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: row;
    box-sizing: border-box;
    padding: 10px 15px;
    background-color: #fff;
  }

  .supplier-type-btn {
    width:150px;
    height: 34px;
    line-height: 34px;
    color: #999;
    background: #eeeeee;
    border-radius: 15px;
    font-size: 13px;
    overflow: hidden;
    text-align: left;
    padding: 0 15px 0 7px;
  
    .icon {
      position: absolute;
      right: 3px;
      top: 10px;
    }
  }

  .search {
    padding: 0 0 0 10px;
    width:150px;
    height: 34px;
  
    .van-search__content {
      background-color: #eee;
      border-radius: 15px;
      height: 100%;
      align-items: center;
    }
  }

  .no-content {
    padding: 50px 20px;

    p {
      font-size: 14px;
    }

    img {
      width: 120px;
      margin-bottom: 30px;
    }
  }
}
</style>
