<template>
	<div class="demand-page">
		<van-nav-bar fixed border :title="demand.company_name" left-arrow @click-left="goBack" />
		<van-row class="header" type="flex" justify="space-around">
			<van-col span="22">
				<div class="supdetail">
					<van-image width="100" height="80" fit="cover" :src="demand.logo" >
						<template v-slot:error>
							<img width="100%" height="80" style="object-fit: cover;" src="http://tianji.unspace.cn/static/v2/img/loading-3.jpg" alt="">
						</template>
						<template v-slot:loading>
								<img width="100%" height="80" style="object-fit: cover;" src="http://tianji.unspace.cn/static/v2/img/loading-3.jpg" alt="">
						 </template>
					</van-image>
					<div class="infos">
						<p class="company">{{demand.company_name}}</p>
						<van-tag class="tag" color="#00c08b" v-if="demand.username">{{ demand.username }}</van-tag>
						<!-- <span class="num">500人以上</span> -->
					</div>
				</div>
				<div>
					<p class="company">主营业务:</p>
					<p class="" v-html="demand.business"></p>
					<p class="address"><i class="iconfont iconbiaodiandidian"></i>{{ demand.address }}</p>
				</div>
			</van-col>
		</van-row>
		<van-row class="b" type="flex" justify="space-around">
			<van-col span="22">
				<div>
					<p class="company">公司简介:</p>
					<div class="depict" v-html="demand.detail"></div>
					<div class="deta_img">
						<van-image v-for="(item,index) in demand.atlas" :key="index" width="80" height="80" fit="cover" :src="item" >
							<template v-slot:error>
								<img width="100%" height="80" style="object-fit: cover;" src="http://tianji.unspace.cn/static/v2/img/loading-3.jpg" alt="">
							</template>
							<template v-slot:loading>
									<img width="100%" height="80" style="object-fit: cover;" src="http://tianji.unspace.cn/static/v2/img/loading-3.jpg" alt="">
							 </template>
						</van-image>
					</div>
				</div>
			</van-col>
		</van-row>
		<van-cell-group>
			<van-cell title="公司联系人:" />
			<van-cell  is-link to="" v-for="(item,index) in demand.contracts" :key="index">
				<van-image width="60" height="60" fit="cover" :src="item.head_img" slot="icon" round />
				<div class="title" slot="title">
					<p>
						<span class="companyName">{{item.username}}</span>
						<!-- <van-tag class="tag" type="danger">{{ demand.type_name }}</van-tag> -->
						<i class="iconfont iconrenzheng1"></i> <i v-if="item.is_company_user" class="iconfont iconqiye1"></i>
					</p>
					<p>
						<span>{{demand.company_name}}</span>
						<span class="position">{{item.position}}</span>
					</p>
				</div slot="title">
			</van-cell>
		</van-cell-group>
		<service-qr-popup ref="serviceQrPopup" />
	</div>
</template>
<script>
	import moment from "moment";
	import {
		mapState
	} from "vuex";
	import {
		Button,
		Row,
		Col,
		NavBar,
		CellGroup,
		Panel,
		Cell,
		PullRefresh,
		List,
		Icon,
		Tag,
		ImagePreview,
		Image
	} from "vant";
	import {
		getSupplierDetail
	} from "../../api/v3";
	import {
		setShare
	} from "../../wechat";
	import {
		PAGE_BASE_URL
	} from "../../config";
	import ServiceQrPopup from "../../components/service-qr-popup";

	export default {
		name: "about",
		components: {
			[Row.name]: Row,
			[Col.name]: Col,
			[NavBar.name]: NavBar,
			[Button.name]: Button,
			[Cell.name]: Cell,
			[Icon.name]: Icon,
			[Image.name]: Image,
			[Tag.name]: Tag,
			[List.name]: List,
			[PullRefresh.name]: PullRefresh,
			[Panel.name]: Panel,
			[CellGroup.name]: CellGroup,
			ServiceQrPopup
		},
		data() {
			return {
				moment,
				demand: {
					reason_data: []
				},
				images: []
			};
		},
		computed: {
			...mapState(["from"])
		},
		async mounted() {
			this.fetchDemand();
		},
		methods: {
			async fetchDemand() {
				this.demand = await getSupplierDetail({
					id: this.$route.params.id
				});
				this.images = this.demand.atlas || [];
				console.log(this.demand)
				const desc = `供应商：${this.demand.company}。点击查看需求详情`;
				const link = PAGE_BASE_URL + "#/suppliers/" + this.demand.id;
				setTimeout(() => {
					setShare({
						title: "天集社", // 分享标题
						desc,
						link
					});
				}, 1000);
			},
			// 点击返回按钮
			goBack() {
				if (this.from) {
					this.$router.back();
				} else {
					this.$router.replace("/suppliers");
				}
			},
			showQr() {
				this.$refs["serviceQrPopup"].show();
			},
			// 预览图片
			previewImg(index) {
				ImagePreview({
					images: this.images,
					startPosition: index,
					lazyLoad: true,
					onClose() {
						// do something
						console.log("close image preview");
					}
				});
			}
		}
	};
</script>

<style lang="less" scoped>
	.iconrenzheng1,.iconqiye1{
		color: #D30023;
		margin:0 2px;
	}	
	.company{
		font-weight: bold;
	}
	
	.address,.depict{
		color: #666;
		font-size: 14px;
	}
	.header{
		margin:10px 0;
	}
	.num{
		margin-left: 10px;
	}
	.b{
		border-top:1px solid #F8F8F8;
		border-bottom:1px solid #F8F8F8;
	}
	p{
		margin:5px 0;
	}
	.infos{
		margin-left: 10px;
	}
	.supdetail {
		display: flex;
		align-items: center;
		// justify-content: space-between;
	}

	.deta_img {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;
		margin: 10px 0;
	}
	.deta_img .van-image{
		margin: 10px 0;
	}
	.title p{
		// display: block;
		margin:5px;
	}
	.position{
		margin-left: 10px;
	}
	.companyName{
		margin-right: 10px;
	}
</style>
