import fetch from "../utils/fetch";

// 获取行业列表
export function getIndustryList({ pid }) {
  return fetch({
    url: "/get_industry_list",
    method: "get",
    params: {
      pid
    }
  });
}

// 获取供应商类型
export function getSupplierTypes() {
  return fetch({
    url: "/supplier/types",
    method: "get"
  });
}

// 获取供应商列表
export function getSupplierList({ type_id, company, page, pagesize,industry_id}) {
  return fetch({
    url: "/supplier",
    method: "get",
    params: {
      type_id,
      company,
      page,
      pagesize,
	  industry_id
    }
  });
}

// 供应商详情
export function getSupplierDetail({ id }) {
  return fetch({
    url: "/supplier/detail",
    method: "get",
    params: {
      supplier_id: id
    }
  });
}

// 聊天记录
export function getDemandChatList(params) {
  return fetch({
    url: "/demandChat",
    method: "get",
    params
  });
}

export function sendChatText({ user_id, demand_id, content }) {
  return fetch({
    url: "/demandChat/send_text",
    method: "post",
    data: {
      user_id,
      demand_id,
      content
    }
  });
}

export function sendChatImage({ user_id, demand_id, media_id }) {
  return fetch({
    url: "/demandChat/send_image",
    method: "post",
    data: {
      user_id,
      demand_id,
      media_id
    }
  });
}

export function sendChatVoice({ user_id, demand_id, media_id }) {
  return fetch({
    url: "/demandChat/send_audio",
    method: "post",
    data: {
      user_id,
      demand_id,
      media_id
    }
  });
}

export function sendChatVideo(data) {
  return fetch({
    url: "/demandChat/send_video",
    method: "post",
    headers: {
      "Content-Type": "multipart/form-data"
    },
    data
  });
}

export function sendChatFile(data) {
  return fetch({
    url: "/demandChat/send_file",
    method: "post",
    headers: {
      "Content-Type": "multipart/form-data"
    },
    data
  });
}

export function getDemandAbutmentUsers(demand_id) {
  return fetch({
    url: "/get_demand_abutment_users",
    method: "get",
    params: {
      demand_id
    }
  });
}

export function uploadImage(data) {
  return fetch({
    url: "/upload_image",
    method: "post",
    headers: {
      "Content-Type": "multipart/form-data"
    },
    data
  });
}

export function getActivityDetail(activity_id, user_id) {
  return fetch({
    url: "/activity_detail",
    method: "get",
    params: {
      activity_id,
      user_id
    }
  });
}

export function applyActivity({ user_id, activity_id, num }) {
  return fetch({
    url: "/activity_apply",
    method: "post",
    data: {
      user_id,
      activity_id,
      num
    }
  });
}
