<template>
  <div class="tabbar-page">
    <div class="tabbar-page-content">
		<keep-alive>
		  <router-view class="tabbar-item-page" />
		</keep-alive>
     <!-- <transition name="van-slide-right">
       
      </transition> -->
    </div>

    <van-tabbar
      fixed
      class="tabbar"
      :value="activeTabIndex"
      active-color="#c11823"
    >
      <van-tabbar-item replace :to="demandListRoute">
        <van-icon slot="icon" class-prefix="tj-icon" name="xuqiu" />
        <span>首页</span>
      </van-tabbar-item>
      <van-tabbar-item replace :to="connectionListRoute">
        <van-icon slot="icon" class-prefix="tj-icon" name="renmai" />
        <span>人脉</span>
      </van-tabbar-item>
      <van-tabbar-item replace :to="supplierListRoute">
        <van-icon slot="icon" class-prefix="tj-icon" name="qiye" />
        <span>供应商</span>
      </van-tabbar-item>
      <van-tabbar-item replace to="/activities">
        <van-icon slot="icon" class-prefix="tj-icon" name="huodong" />
        <span>发现</span>
      </van-tabbar-item>
      <van-tabbar-item @click="toMyself">
        <van-icon slot="icon" class-prefix="tj-icon" name="wode" />
        <span>我</span>
      </van-tabbar-item>
    </van-tabbar>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
import { Tabbar, TabbarItem, Icon } from "vant";

export default {
  name: "TabbarLayout",
  components: {
    [Icon.name]: Icon,
    [Tabbar.name]: Tabbar,
    [TabbarItem.name]: TabbarItem
  },
  data() {
    return {
      demandListRoute: { name: "demand-list" },
      supplierListRoute: { name: "supplier-list" },
      connectionListRoute: { name: "connection-list", query: { tab: "square" } }
    };
  },
  computed: {
    ...mapGetters(["userInfo"]),
    activeTabIndex() {
      const routeName = this.$route.name;
      switch (routeName) {
        case "demand-list":
          return 0;
        case "connection-list":
          return 1;
        case "supplier-list":
          return 2;
        case "activity-list":
          return 3;
        case "user-center":
          return 4;
        default:
          return 0;
      }
    }
  },
  watch: {
    $route(to, from) {
      // 记录需求列表的路由状态
      if (from.name === "demand-list") {
        this.demandListRoute = { ...from };
      } else if (from.name === "connection-list") {
        this.connectionListRoute = { ...from };
      }
    }
  },
  methods: {
    toMyself() {
      if (this.userInfo.status === "0") {
        this.$router.push("/my-card");
      } else {
        this.$router.replace("/user-center");
      }
    }
  }
};
</script>
<style lang="less" scoped>
.tabbar-page {
  display: flex;
  flex-direction: column;
  padding-top: 0;
  padding-bottom: 50px;
  box-sizing: border-box;
}

.tabbar-page-content {
  flex: 1;
  overflow: hidden;
  position: relative;
}

.tabbar-item-page {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow-y: auto;
}

.tj-icon {
  font-size: 22px;
}

@media (min-width: 750px) {
  .tabbar-page {
    padding-bottom: 50px;
  }

  .tj-icon {
    font-size: 22px;
  }
}
</style>
