import axios from "axios";
import qs from "qs";
import store from "../store";
import { API_BASE_URL } from "../config";
import router from "../router";

// 创建axios实例
const fetch = axios.create({
  baseURL: API_BASE_URL,
  timeout: 10 * 1000, // 请求超时时间
  headers: {
    // crossDomain: true, // 跨域
    "Content-Type": "application/x-www-form-urlencoded",
    // "Content-Type": "application/json",
	// "Authorization": "Bearer " + sessionStorage.getItem("token")
  }
});

window.fetch = fetch;

// request拦截器
fetch.interceptors.request.use(
  config => {
    // Do something before request is sent
    // const { token } = store.state;
	let token = sessionStorage.getItem("token")
	// let token = "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJodHRwOlwvXC90aWFuamkudW5zcGFjZS5jbiIsImlhdCI6MTU4NTcxMDc1NywiZXhwIjoxNTg4MzAyNzU3LCJqdGkiOiJhNWQ2NGI4ODc5MzAyMWUxNWY2ZjI5NDEwZTgzYjNkYyIsInVzZXIiOnsiaWQiOiIxNTcxIn19.WhaG3NlfxXy0gLe1EpR2PsWMkR0EP_ye8NjnpoE5qeE"
    if (token) {
      // 让每个请求携带token--['X-Token']为自定义key 请根据实际情况自行修改
      config.headers.authorization = `Bearer ${token}`; // eslint-disable-line no-param-reassign
      // config.headers.token = token; // eslint-disable-line no-param-reassign
    }
    if (
      config.data &&
      config.headers["Content-Type"] === "application/x-www-form-urlencoded"
    ) {
      config.data = qs.stringify(config.data); // eslint-disable-line no-param-reassign
    }
    return config;
  },
  error => {
    // Do something with request error
    console.log(error); // for debug
    Promise.reject(error);
  }
);

// respone拦截器
fetch.interceptors.response.use(
  response => {
    const {
      data: { err, msg, data }
    } = response;
    //	 console.log(data,8888)
    if (err >= 0 || !err) {
      return data;
    } else if (data == "coods:789") {
      if (err == -3) {
        //		 console.log(err)
        //		  console.log(msg)
        router.replace("/BlackDemand");
      }
    } else {
      //		console.log(err)
      //		 console.log(msg)
      throw new Error(msg);
	  
    }
  },
  error => {
    console.log(error);
    const {
      response: { data, status }
    } = error;
    console.log(error, data, status);

    return Promise.reject(error.response);
  }
);

export default fetch;
