<template>
  <div class="butts-page">
    <div class="total">对接人数：{{ count }}人</div>

    <van-row class="list">
      <van-col span="4" class="item" v-if="author">
        <simple-avatar
          :item="author"
          :isOwner="true"
          @click="handleAvatarClick"
        />
      </van-col>
      <van-col span="4" v-for="item in users" :key="item.user_id" class="item">
        <simple-avatar :item="item" @click="handleAvatarClick" />
      </van-col>
    </van-row>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { Col, Row } from "vant";
import { getDemandAbutmentUsers } from "../../api/v3";
import SimpleAvatar from "../../components/simple-avatar";

export default {
  name: "butts-page",
  props: ["demand"],
  components: {
    [Col.name]: Col,
    [Row.name]: Row,
    SimpleAvatar
  },
  data() {
    return {
      loading: false,
      author: null,
      count: 0,
      users: []
    };
  },
  computed: {
    ...mapGetters(["userInfo"]),
    demandId() {
      return this.$route.params.id;
    }
  },
  watch: {
    demandId() {
      this.fetchData();
    }
  },
  mounted() {
    this.fetchData();
  },
  methods: {
    async fetchData() {
      this.loading = true;
      const { count, users } = await getDemandAbutmentUsers(this.demandId);
      this.author = users.find(item => item.user_id === this.demand.user_id);
      this.users = users.filter(item => item.user_id !== this.demand.user_id);
      this.count = count;
      this.loading = false;
    },
    handleAvatarClick(item) {
      this.$router.push(`/cards/${item.user_id}`);
    }
  }
};
</script>
<style lang="less" scoped>
.butts-page {
  background-color: #fff;
}

.total {
  text-align: center;
  padding: 20px 10px;
}
.list {
  padding: 0 10px 20px;
}

.item {
  text-align: center;
}

@media screen and (min-width: 750px) {
  .total {
    padding: 20px 10px;
  }
  .list {
    padding: 0 10px 20px;
  }
}
</style>
